import characters from "../data/characters";
import { infusePrior, rsnNames } from "../data/elements";
import {
  getMainArtBCs,
  getMainWpBCs,
  getSubArtBCs,
  getSubArtDCs,
  getSubWpBCs,
  getVisionCount,
  getWpRange,
  initCharMCs
} from "../components/Calculator/helpers";

export function getArtSets(pieces = []) {
  const sets = [];
  const count = {};
  for (const artP of pieces) {
    if (artP) {
      const name = artP.set;
      count[name] = (count[name] || 0) + 1;
      if (count[name] === 2) {
        sets.push({ name, bonusLv: 0 });
      } else if (count[name] === 4) {
        sets[0].bonusLv = 1;
      }
    }
  }
  return sets;
}

export function processNumInp(inp, before, max) {
  if (inp === "") return 0;
  if (!isNaN(inp) && inp >= 0 && inp <= max) {
    if (inp.slice(-1) === ".") return inp;
    return Math.round(inp * 10) / 10;
  }
  return before;
}

export function getFinalInfusion(char, selfBCs, ownVision, party) {
  const selfInfusion = [];
  for (const ctrl of selfBCs) {
    const buff = characters[char.name].buffs.find(
      (b) => b.index === ctrl.index
    );
    if (buff && buff.canInfuse && ctrl.activated && buff.isGranted(char)) {
      const { infuseRange: range, canBeOverrided } = buff;
      selfInfusion.push({ range, canBeOverrided });
    }
  }
  const tmInfusion = [];
  for (const tm of party) {
    if (tm) {
      for (const ctrl of tm.BCs) {
        const buff = characters[tm.name].buffs.find(
          (b) => b.index === ctrl.index
        );
        if (
          buff &&
          buff.canInfuse &&
          ctrl.activated &&
          buff.canInfuse(characters[char.name])
        ) {
          tmInfusion.push({
            elmt: characters[tm.name].vision,
            range: buff.infuseRange
          });
        }
      }
    }
  }
  const result = {};
  for (const type of ["NA", "CA", "PA"]) {
    result[type] = strongestInfusion(type, selfInfusion, ownVision, tmInfusion);
  }
  return result;
}

function strongestInfusion(type, selfInfusion, ownVision, tmInfusion) {
  const contenders = ["Physical"];
  for (const ifs of selfInfusion) {
    if (ifs.range.includes(type)) {
      if (!ifs.canBeOverrided) {
        return ownVision;
      } else if (!contenders.includes(ownVision)) {
        contenders.unshift(ownVision);
      }
    }
  }
  for (const ifs of tmInfusion) {
    for (const index in contenders) {
      if (
        infusePrior.indexOf(ifs.elmt) < infusePrior.indexOf(contenders[index])
      ) {
        // no need infusion.range yet
        contenders.splice(index, 0, ifs.elmt);
        break;
      }
    }
  }
  return contenders[0];
}

export function filterAllMCs(data) {
  const { char, subWpMCs, art, selfMCs, weapon, elmtMCs } = data;
  function filter(list) {
    return list.filter((elmt) => elmt.activated);
  }
  function filterSelfMCs(list, type) {
    const modList = characters[char.name][type];
    return filter(list).filter((ctrl) => {
      if (!modList) return false;
      const mod = modList.find(({ index }) => index === ctrl.index);
      return mod && mod.isGranted(char);
    });
  }
  const subWpBCs = {};
  for (const type in subWpMCs.BCs) {
    subWpBCs[type] = filter(subWpMCs.BCs[type]);
  }
  const artMCs = {};
  for (const type of ["BCs", "subBCs", "subDCs"]) {
    artMCs[type] = filter(art[type]);
  }
  return {
    selfMCs: {
      BCs: filterSelfMCs(selfMCs.BCs, "buffs"),
      DCs: filterSelfMCs(selfMCs.DCs, "debuffs")
    },
    weapon: { ...weapon, BCs: filter(weapon.BCs) },
    subWpMCs: { BCs: subWpBCs, MCs: [] },
    art: { ...art, ...artMCs },
    party: data.party.map((tm) => {
      if (!tm) return null;
      return { name: tm.name, BCs: filter(tm.BCs), DCs: filter(tm.DCs) };
    }),
    elmtMCs: { ...elmtMCs, resonance: filter(elmtMCs.resonance) }
  };
}

export function adjustDB(data) {
  if (!data.Setups) data.Setups = [];
  const allOutdates = [];
  if (!data.version) {
    for (const char of data.Characters) {
      const { flower, plume, sands, goblet, circlet } = char.artIDs;
      char.artIDs = [flower, plume, sands, goblet, circlet];
    }
    for (const i in data.Setups) {
      if (data.Setups[i].type === "complex") continue;
      for (const tmI in data.Setups[i].party) {
        if (!data.Setups[i].party[tmI].name) {
          data.Setups[i].party[tmI] = null;
        } else {
          delete data.Setups[i].party[tmI].infusion;
        }
      }
      const { ID, name, type, char, target } = data.Setups[i];
      data.Setups[i] = {
        ID,
        name,
        type,
        char,
        ...filterAllMCs(data.Setups[i]),
        target
      };
    }
  } else {
    function extractOutdates(list, name, type) {
      const outdates = [];
      const filtered = [];
      for (const item of list) {
        const { index } = item;
        const mod = characters[name][type].find((m) => m.index === index);
        if (!mod) continue;
        if (mod.outdated) {
          outdates.push({ name, type, index });
        } else filtered.push(item);
      }
      return [filtered, outdates];
    }
    for (const st of data.Setups) {
      if (st.type === "complex") continue;
      const { name } = st.char;
      const list = [];
      for (const ctrlType of ["BCs", "DCs"]) {
        const type = ctrlType === "BCs" ? "buffs" : "debuffs";
        const extracted1 = extractOutdates(st.selfMCs[ctrlType], name, type);
        st.selfMCs[ctrlType] = extracted1[0];
        list.push(...extracted1[1]);
        for (const tm of st.party) {
          if (!tm) continue;
          const extracted2 = extractOutdates(tm[ctrlType], tm.name, type);
          tm[ctrlType] = extracted2[0];
          list.push(...extracted2[1]);
        }
      }
      if (list.length) {
        allOutdates.push({
          name: st.name,
          members: [name].concat(
            st.party.filter((tm) => tm).map((tm) => tm.name)
          ),
          list
        });
      }
    }
  }
  return [data, allOutdates.length ? allOutdates : null];
}

export function restoreAllMCs(data) {
  function restoreMCs(oldMCs, newMCs) {
    for (const ctrl of oldMCs) {
      const i = newMCs.findIndex((c) => c.index === ctrl.index);
      if (i >= 0) {
        newMCs[i].activated = true;
        if (ctrl.inputs && newMCs[i].inputs) {
          newMCs[i].inputs = ctrl.inputs;
        }
      }
    }
  }
  const { char, weapon, elmtMCs } = data;
  const selfMCs = initCharMCs(char.name, true);
  for (const type of ["BCs", "DCs"]) {
    restoreMCs(data.selfMCs[type], selfMCs[type]);
  }
  const mainWpBCs = getMainWpBCs(weapon.type, weapon.name);
  restoreMCs(weapon.BCs, mainWpBCs);

  const subWpBCs = {};
  for (const type in getWpRange(data.party)) {
    subWpBCs[type] = getSubWpBCs(type, weapon.name);
    for (const ctrl of data.subWpMCs.BCs[type]) {
      const i = subWpBCs[type].findIndex(
        (c) => c.name === ctrl.name && c.index === ctrl.index
      );
      if (i >= 0) {
        subWpBCs[type][i].activated = true;
        subWpBCs[type][i].refinement = ctrl.refinement;
      }
    }
  }
  const setName = data.art.sets[0]?.name;
  const artMCs = {
    BCs: getMainArtBCs(setName),
    subBCs: getSubArtBCs(setName),
    subDCs: getSubArtDCs()
  };
  for (const type of ["BCs", "subBCs", "subDCs"]) {
    restoreMCs(data.art[type], artMCs[type]);
  }
  const party = [null, null, null];
  for (const tmI in data.party) {
    if (data.party[tmI]) {
      const { name } = data.party[tmI];
      party[tmI] = { name, ...initCharMCs(name, false) };
      for (const type of ["BCs", "DCs"]) {
        restoreMCs(data.party[tmI][type], party[tmI][type]);
      }
    }
  }
  const resonance = [];
  const vsc = getVisionCount(char, party);
  for (const elmt in vsc) {
    if (vsc[elmt] >= 2) {
      const name = rsnNames[elmt];
      resonance.push({
        name,
        activated:
          elmtMCs.resonance.find((rsn) => rsn.name === name)?.activated || false
      });
    }
  }
  return {
    selfMCs,
    weapon: { ...data.weapon, BCs: mainWpBCs },
    subWpMCs: {
      BCs: subWpBCs,
      DCs: {}
    },
    art: { ...data.art, ...artMCs },
    party,
    elmtMCs: { ...elmtMCs, resonance }
  };
}
