import styled from "styled-components";
import { colorCodes } from "../../../data/misc";

export const Divider = styled.div`
  width: 2px;
  height: 100%;
  background-color: ${colorCodes.cardBg3};
  margin: 0 1rem;
`;

export const LinkIcon = styled.button`
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  color: white;
  font-size: 1rem;
  &.link {
    background-color: transparent;
  }
  &.unlink {
    background-color: ${colorCodes.red};
  }
`;

export const MemberWrapper = styled.div`
  position: relative;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  &.clickable {
    box-shadow: 0 0 3px 3px ${colorCodes.lightGold};
    cursor: pointer;
  }
  :not(:last-child) {
    margin-right: 1rem;
  }
  .calc-btn {
    position: absolute;
    bottom: -0.25rem;
    right: -0.25rem;
    display: none;
  }
  :hover .calc-btn {
    display: block;
  }
`;
