import { useState, useContext, useCallback } from "react";
import { MyArtsCt } from "../../App";
import {
  Box,
  AbsoluteBox,
  HiddenSpace,
  ModalCarpet,
  ModalInner
} from "../../styledCpns/Layout";
import { StyledHeading, Cell, Card } from "../../styledCpns/Database";
import { IconBtn } from "../../styledCpns/Inputs";
import { Text } from "../../styledCpns/DataDisplay";
import { ButtonBar, EquippedChar, CloseBtn, NoItems } from "../CommonCpns";
import ArtFilter from "./ArtFilter";
import Thumb from "./Thumb";
import { MyArtCard } from "./MyItemCard";
import artifacts from "../../data/artifacts";
import { artNames, artTypes } from "../../data/misc";
import { getArts } from "../MyArtifacts";
import { useCloseModal } from "../../helpers/hooks";
import { initArtStatFilter } from "../../helpers/misc";

export default function ArtInventory(props) {
  const { artType, close } = props;
  const [filterOn, setFilterOn] = useState(false);
  const [comparing, setComparing] = useState(false);
  const curArt = props.curPieces[artTypes.indexOf(artType)];

  const myArtifacts = useContext(MyArtsCt);
  const data = myArtifacts.filter((art) => art.type === artType);
  const [stats, setStats] = useState(initArtStatFilter());
  const [sets, setSets] = useState([]);
  const filteredData = getArts(data, [], sets, stats);
  const [chosenID, setChosenID] = useState(filteredData[0]?.ID || 0);
  const chosenArt = filteredData.find(({ ID }) => ID === chosenID);

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <ModalInner ref={ref} className="no-select">
        <Box height="10%" className="p-2">
          <StyledHeading>
            {window.innerWidth > 380 && (
              <p className="heading_text">{artNames[artType]}</p>
            )}
            <CloseBtn className="close_btn" onClick={close} />
            <IconBtn
              icon="fas fa-filter"
              className={"filter_btn glow-on-hover" + (filterOn ? " on" : "")}
              onClick={() => setFilterOn(!filterOn)}
            />
            <ArtFilter
              filterOn={filterOn}
              arts={data}
              artType={artType}
              filter={{ stats, setStats, sets, setSets }}
              close={() => setFilterOn(false)}
            />
          </StyledHeading>
        </Box>
        <Box height="90%" className="pt-2 pr-3 pb-3 pl-2">
          <div className="full-h flex hide-sb">
            {filteredData.length ? (
              <Rack
                data={filteredData}
                chosenID={chosenID}
                setChosenID={setChosenID}
              />
            ) : (
              <Box className="grow-1" minWidth="18.75rem">
                <NoItems type={artNames[artType]} />
              </Box>
            )}
            <div className="flex-col justify-between">
              <Card className="relative">
                {curArt ? (
                  <AbsoluteBox
                    className="full-h"
                    top={0}
                    right="calc(100% - 1rem)"
                    zIndex={1}
                  >
                    <HiddenSpace
                      className={"full-h hide-sb" + (comparing ? " show" : "")}
                      openWidth="15.75rem"
                    >
                      <Box
                        className="pl-3 pr-2 py-3 full-h flex-col"
                        width="15.75rem"
                        bgColor="cardBg1"
                        borderRadius="0.5rem 0 0 0.5rem"
                      >
                        <MyArtCard art={curArt} mutable={false} ssSpace={3} />
                        <div className="pt-3 grow-1 flex-center">
                          <Text align="center" color="orange">
                            Current equipment
                          </Text>
                        </div>
                      </Box>
                    </HiddenSpace>
                  </AbsoluteBox>
                ) : null}
                <Box className="hide-sb" width="16rem" height="24rem">
                  {chosenArt ? (
                    <MyArtCard art={chosenArt} mutable={false} ssSpace={3} />
                  ) : null}
                </Box>
                {chosenArt && chosenArt.user !== props.owner ? (
                  <ButtonBar
                    className="mt-4"
                    variants={[comparing ? "neutral" : "default", "positive"]}
                    texts={["Compare", props.btnText]}
                    availables={[Boolean(curArt), true]}
                    handlers={[
                      () => {
                        if (curArt) setComparing(!comparing);
                      },
                      () => {
                        props.btnClick(chosenArt);
                        close();
                      }
                    ]}
                  />
                ) : null}
              </Card>
              {chosenArt?.user ? <EquippedChar user={chosenArt.user} /> : null}
            </div>
          </div>
        </Box>
      </ModalInner>
    </ModalCarpet>
  );
}

function Rack({ data, chosenID, setChosenID }) {
  const [clickedI, setClickedI] = useState(null);
  const choose = useCallback(
    (id) => {
      setClickedI(null);
      setChosenID(id);
    },
    [setChosenID]
  );
  return (
    <Box
      className="pr-2 grow-1 full-h overflow-auto hide-sb"
      minWidth="18.75rem"
    >
      <div className="flex-wrap">
        {data.map(({ set, type, ID, user, rarity, level }, i) => {
          const { icon } = artifacts[set][type];
          return (
            <Cell key={ID}>
              <Thumb
                index={i}
                item={{ ID, user, rarity, level, icon }}
                chosen={ID === chosenID}
                clicked={i === clickedI}
                handleMouseDown={setClickedI}
                choose={choose}
              />
            </Cell>
          );
        })}
      </div>
    </Box>
  );
}
