import { useContext, useState } from "react";
import { MyStsCt, SetDBCt } from "../../../../App";
import { useCloseModal } from "../../../../helpers/hooks";
import { deepCopy } from "../../../../helpers/misc";
import { filterAllMCs } from "../../../../helpers/processors";
import { Text } from "../../../../styledCpns/DataDisplay";
import { TextInput } from "../../../../styledCpns/Inputs";
import { Box, ModalCarpet } from "../../../../styledCpns/Layout";
import { ButtonBar, ShareLayout } from "../../../CommonCpns";
import {
  ArtChestCt,
  CharCt,
  ElmtMCsCt,
  PartiesCt,
  SelfMCsCt,
  SubWpMCsCt,
  TargetCt,
  WpRackCt
} from "../../index";

export function SaveCtrl({
  savedSetup: { name, ID, index },
  closeSettings,
  close
}) {
  const char = useContext(CharCt);
  const { setMySts, setTab, setChosenStID } = useContext(SetDBCt);
  const mySetups = useContext(MyStsCt);
  const existedI = mySetups.findIndex((st) => st.ID === ID);
  const isExisted = existedI >= 0;
  const [input, setInput] = useState(
    isExisted ? mySetups[existedI].name : char.name + " - " + name
  );
  const data = useProcessData(index);

  const saveSetup = () => {
    let newID = ID;
    let parentStI;
    if (isExisted && mySetups[existedI].type === "combined") {
      parentStI = mySetups.findIndex(
        (st) => st.type === "complex" && st.allIDs[char.name] === ID
      );
      if (parentStI >= 0) {
        newID = mySetups[parentStI].ID;
      }
    }
    setMySts((prev) => {
      const sts = [...prev];
      if (parentStI >= 0) {
        sts[parentStI].shownID = ID;
      }
      const newSetup = {
        name: input,
        ID,
        type: isExisted ? sts[existedI].type : "original",
        ...data
      };
      if (isExisted) sts[existedI] = newSetup;
      else sts.unshift(newSetup);
      return sts;
    });
    closeSettings();
    setChosenStID(newID);
    setTab("Setups");
  };

  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-4 flex-col white-glow-shadow max-width-95"
        borderRadius="0.5rem"
        bgColor="cardBg1"
      >
        <Text
          className="mb-2 mx-auto"
          variant="h5"
          color="orange"
          fontWeight={700}
        >
          {existedI >= 0 ? "Modify OLD" : "Save NEW"} Setup
        </Text>
        <TextInput
          className="mt-1 mb-5 px-3 pt-3 pb-2"
          type="text"
          borderRadius="0.25rem"
          fontSize="1.5rem"
          align="center"
          value={input}
          onChange={(e) => {
            const { value } = e.target;
            if (value.length <= 32) setInput(e.target.value);
          }}
          onKeyDown={(e) => {
            if (e.key === "Enter") {
              saveSetup();
              close();
            }
          }}
        />
        <ButtonBar
          texts={["Cancel", "Confirm"]}
          handlers={[
            close,
            () => {
              saveSetup();
              close();
            }
          ]}
        />
      </Box>
    </ModalCarpet>
  );
}

export function SharedCtrl({ sharedSetup: { name, index }, close }) {
  return (
    <ShareLayout setupName={name} data={useProcessData(index)} close={close} />
  );
}

function useProcessData(index) {
  const char = useContext(CharCt);
  const art = deepCopy(useContext(ArtChestCt)[index]);
  for (const p of art.pieces) {
    if (p) delete p.ID;
  }
  return {
    char,
    ...filterAllMCs({
      char,
      selfMCs: useContext(SelfMCsCt)[index],
      weapon: useContext(WpRackCt)[index],
      subWpMCs: useContext(SubWpMCsCt)[index],
      art: useContext(ArtChestCt)[index],
      party: useContext(PartiesCt)[index],
      elmtMCs: useContext(ElmtMCsCt)[index]
    }),
    target: useContext(TargetCt).target
  };
}
