import { getXtraTalentLv } from "../../../../calculators/helpers";
import { checkCharMC, findInput } from "../../../../helpers/calculators";
import { Green, Hydro, LGreen } from "../../../../styledCpns/DataDisplay";
import {
  CaStamina,
  mediumPAs,
  SwordNaDesc,
  SwordPaDesc,
  TalentLvMults
} from "../../config";
import Ayato from "./index";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Kamisato Art - Korobi",
    image: "a/a2/Character_Kamisato_Ayato_Thumb",
    desc: [
      { heading: "NA", content: SwordNaDesc },
      {
        heading: "CA",
        content: (
          <>
            Consumes a certain amount of Stamina to dash forward and perform an
            iai.
          </>
        )
      },
      { heading: "PA", content: SwordPaDesc }
    ],
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 44.96,
        multType: 7
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 47.16,
        multType: 7
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 58.61,
        multType: 7
      },
      {
        name: "4-Hit (x2)",
        dmgTypes: ["NA", "Physical"],
        baseMult: 29.45,
        multType: 7
      },
      {
        name: "5-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 75.6,
        multType: 7
      },
      {
        name: "Charged Attack",
        dmgTypes: ["CA", "Physical"],
        baseMult: 129.53,
        multType: 7
      },
      CaStamina(20),
      ...mediumPAs
    ]
  },
  {
    type: "Elemental Skill",
    name: "Kamisato Art: Kyouka",
    image: "a/a2/Character_Kamisato_Ayato_Thumb",
    desc: [
      {
        content: (
          <>
            Kamisato Ayato shifts positions, leaving a watery illusion at his
            original location and entering the Soukai Kanka state.
            <br />
            The watery illusion will taunt nearby opponents and explode after a
            certain time or when attacked, dealing AoE <Hydro>Hydro DMG</Hydro>.
          </>
        )
      },
      {
        heading: "Soukai Kanka",
        get content() {
          return (
            <>
              In this state, Kamisato Ayato uses his Shunsuiken to engage in
              blindingly fast attacks, causing {this.lines[0]}
              <br />
              It also has the following properties:
              <br /> After a Shunsuiken attack hits an opponent, it will grant
              Ayato the Wave Flash effect, increasing {this.lines[1]} The
              initial maximum number of Wave Flash <LGreen>stacks</LGreen> is{" "}
              <Green>4</Green>, and 1 stack can be gained through Shunsuiken
              every 0.1s. This effect will be dispelled when Soukai Kanka ends.
              <br /> Kamisato Ayato's resistance to interruption is increased.
              <br /> Unable to use Charged or Plunging Attacks.
            </>
          );
        },
        lines: [
          <>
            <LGreen>DMG</LGreen> from his <LGreen>Normal Attacks</LGreen> to be{" "}
            <LGreen>converted</LGreen> into AoE <Hydro>Hydro DMG</Hydro>. This
            cannot be overridden.
          </>,
          <>
            the <LGreen>DMG</LGreen> dealt by <LGreen>Shunsuiken</LGreen> based
            on Ayato's <LGreen>current Max HP</LGreen>.
          </>
        ]
      },
      {
        content: (
          <>
            Soukai Kanka will be cleared when Ayato leaves the field. Using
            Kamisato Art: Kyouka again while in the Soukai Kanka state will end
            that state.
          </>
        )
      },
      {
        content: (
          <>
            Additionally, Kamisato Art: Kyouka's CD will decrease when the
            Normal Attacks of nearby party members hit opponents.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Shunsuiken 1-Hit DMG",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 43.52,
        multType: 7,
        getTlBnes: getShunsuikenTlBnes
      },
      {
        name: "Shunsuiken 2-Hit DMG",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 46.1,
        multType: 7,
        getTlBnes: getShunsuikenTlBnes
      },
      {
        name: "Shunsuiken 3-Hit DMG",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 48.68,
        multType: 7,
        getTlBnes: getShunsuikenTlBnes
      },
      {
        name: "Extra Shunsuiken strike (x2) (C6)",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 0,
        multType: 7,
        conditional: true,
        getTlBnes: ({ char, selfMCs: { BCs } }) => ({
          xtraDmgBn: getShunsuikenXtraPctDmg(char, BCs),
          xtraMult: char.constellation >= 6 ? 300 : 0
        })
      },
      {
        name: "Soukai Kanka Duration",
        noCalc: true,
        getValue: () => "6s"
      },
      {
        name: "Wave Flash DMG Bonus",
        noCalc: true,
        getValue: (lv) =>
          Math.round(44 * TalentLvMults[7][lv]) / 100 + "% Max HP/stack"
      },
      {
        name: "Wave Flash DMG Bonus",
        baseSType: "HP",
        baseMult: 0,
        multType: 7,
        conditional: true,
        getTlBnes: ({ char, selfMCs: { BCs }, party }) => {
          const level =
            char["Elemental Skill"] +
            getXtraTalentLv(Ayato.actvTalents[1], char, party);
          return {
            xtraMult: 0.44 * findInput(BCs, 0, 0) * TalentLvMults[7][level]
          };
        }
      },
      {
        name: "Water Illusion DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 101.48,
        multType: 7
      }
    ],
    otherStats: () => [
      {
        name: "Water Illusion Duration",
        value: "6s"
      },
      {
        name: "Reduced CD",
        value: "2s"
      },
      {
        name: "Shortest Trigger Interval",
        value: "2s"
      },
      {
        name: "CD",
        value: "20s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Kamisato Art: Suiyuu",
    image: "a/a2/Character_Kamisato_Ayato_Thumb",
    desc: [
      {
        content: (
          <>
            Unveils a garden of purity that extinguishes all cacophony within.
            <br />
            While this space exists, Bloomwater Blades will constantly rain down
            and attack opponents within its AoE, dealing{" "}
            <Hydro>Hydro DMG</Hydro> and increasing the <LGreen>ATK SPD</LGreen>{" "}
            of characters within (excluding Ayato himself).
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Bloomwater Blade DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 55.02,
        multType: 2
      }
    ],
    otherStats: (lv) => [
      {
        name: "ATK SPD Bonus",
        value: Ayato.buffs[1].bnValue(lv) + "%"
      },
      {
        name: "Duration",
        value: "15s"
      },
      {
        name: "CD",
        value: "20s"
      }
    ],
    energyCost: 80
  }
];

export default actvTalents;

function getShunsuikenXtraPctDmg(char, BCs) {
  return checkCharMC(Ayato.buffs, char, BCs, 3) ? 30 : 0;
}

function getShunsuikenTlBnes({ char, selfMCs: { BCs }, ATTRs, party }) {
  let xtraFlat = 0;
  if (checkCharMC(Ayato.buffs, char, BCs, 0)) {
    const level =
      char["Elemental Skill"] +
      getXtraTalentLv(Ayato.actvTalents[1], char, party);
    const totalOgMult = 0.0044 * findInput(BCs, 0, 0);
    xtraFlat = Math.round(ATTRs.HP * totalOgMult * TalentLvMults[1][level]);
  }
  return {
    xtraDmgBn: getShunsuikenXtraPctDmg(char, BCs),
    xtraFlat
  };
}
