import { Gold, LGreen, Red } from "../styledCpns/DataDisplay";

const Updates = [
  {
    date: "February 19th, 2022",
    content: [
      <>
        Fix the DMG of <Gold>Thoma</Gold>'s Elemental Burst was not considered
        as Elemental Burst DMG. Thank you <Gold>r/CosSheCute</Gold> for
        detecting this problem!
      </>
    ]
  },
  {
    date: "February 18th, 2022",
    content: [
      <>
        Give stacks to <Gold>Ayato</Gold>'s Wave Flash effect because I missed
        that.
      </>,
      <>
        Remove unnecessary buff controls. From now on these buffs will be
        controlled by their required ascension / constellation levels:{" "}
        <Gold>Beidou</Gold>'s C4, <Gold>Fischl</Gold>'s A4, C1, C6,{" "}
        <Gold>Yae Miko</Gold>'s C2, <Gold>Yanfei</Gold>'s C4. They only give new
        fields in damage results, not modify existing ones.
      </>
    ]
  },
  {
    date: "February 17th, 2022",
    content: [
      <>
        Add <Gold>Ayato</Gold> (beta) and <Gold>Haran Tsukishiro Futsu</Gold>{" "}
        (sword - beta).
      </>
    ]
  },
  {
    date: "February 7th, 2022",
    content: [
      <>
        You now again can add beta Characters into My Characters, and Setups
        containing beta Characters into My Setups. I've found a better way to
        read the Database.
      </>
    ]
  },
  {
    date: "January 28th, 2022",
    content: [
      <>Add 4-star Artifacts.</>,
      <>You now can remove Characters in My Characters.</>,
      <>
        UI and UX improvements. If you put duplicated stats on an artifact
        piece, or enter invalid sub stats values, they will turn red.
      </>
    ]
  },
  {
    date: "January 21st, 2022",
    content: [
      <>
        Update <Gold>Yae Miko</Gold>'s changes (V4).
      </>
    ]
  },
  {
    date: "January 17th, 2022",
    content: [
      <>
        Update <Gold>Yae Miko</Gold>'s changes.
      </>,
      <>
        Now Setups have main character or teammates that are still in Beta
        cannot be saved as well.
      </>,
      <>
        If you have <Gold>Yae Miko</Gold> in any of your saved Setups, please
        remove those Setups as they can cause the App to malfunction when this
        character gets drastic changes to buffs and debuffs. Sorry for the
        inconveniences.
      </>
    ]
  },
  {
    date: "January 9th, 2022",
    content: [
      <>
        Improve Artifacts Filter. You can also use it when picking artifacts
        from Database.
      </>,
      <>
        Adjust <Gold>Thrilling Tales of Dragon Slayers</Gold>'s Base ATK.
      </>,
      <>
        Fix <Gold>Razor</Gold>'s C3 and C5 upgrading wrong Talents.
      </>
    ]
  },
  {
    date: "January 7th, 2022",
    content: [
      <>
        Add <Gold>Yae Miko</Gold>, <Gold>Kagura's Verity</Gold> (catalyst) and{" "}
        <Gold>Oathsworn Eye</Gold> (catalyst). These unit and items are still in{" "}
        <Red>Beta</Red> stage, and thus subjects to change.
      </>,
      <>Characters in Beta cannot be added to My Characters.</>
    ]
  },
  {
    date: "January 5th, 2022",
    content: [
      <>
        Update icons for <Gold>Shenhe</Gold>, <Gold>Yun Jin</Gold>, and{" "}
        <Gold>Calamity Queller</Gold>.
      </>,
      <>
        Add grid view option when choosing character in My Characters for
        devices that are wider than 700px.
      </>,
      <>Improve My Weapons manager.</>,
      <>Minor UI changes.</>
    ]
  },
  {
    date: "January 1st, 2022",
    content: [
      <>
        Optimize calculations on external infusion. You now can calculate Melt /
        Vaporize DMG on Normal / Charged / Plunging Attacks of any Character
        affected by external infusion, e.g. <Gold>Bennett</Gold>'s C6,{" "}
        <Gold>Chongyun</Gold>'s Elemental Skill. Due to this change in system,
        codes of shared Setup made before this version are no longer functional.
      </>,
      <>
        In My Artifacts, type filter for pieces now starts with no preferred
        types, and no pieces are filtered.
      </>,
      <>
        <LGreen>New Feature</LGreen>: You now can combine Setups with 4 members
        in My Setups, and also build Setups for teammates based on a saved
        Setup. Read more in HOW-TOs.
      </>,
      <>Minor UI and UX improvements.</>,
      <>Update HOW-TOs for Calculator and Database Manager.</>,
      <>Happy New Year!</>
    ]
  }
];

export default Updates;

