import { useCallback, useContext } from "react";
import { MyStsCt, SetDBCt } from "../../App";
import Setup from "./Setup";
import { deepCopy } from "../../helpers/misc";

export default function ComplexSetup(props) {
  const { setup } = props;
  const { allIDs, shownID } = setup;
  const mySetups = useContext(MyStsCt);
  const { setMySts, setChosenStID } = useContext(SetDBCt);
  const index = mySetups.findIndex((st) => st.ID === setup.ID);

  const uncombine = useCallback(() => {
    setMySts((prev) => {
      const sts = deepCopy(prev);
      for (let member in allIDs) {
        const i = sts.findIndex((st) => st.ID === allIDs[member]);
        sts[i].type = "original";
      }
      return sts;
    });
    setTimeout(() => {
      setMySts((prev) => {
        const sts = deepCopy(prev);
        sts.splice(index, 1);
        return sts;
      });
      setChosenStID(shownID);
    }, 100);
  }, [index, shownID, allIDs, setChosenStID, setMySts]);

  const switchMember = useCallback(
    (memberID) =>
      setMySts((prev) => {
        const sts = deepCopy(prev);
        sts[index].shownID = memberID;
        return sts;
      }),
    [index, setMySts]
  );

  return (
    <Setup
      ID={setup.ID}
      topBar={props.topBar}
      setup={props.shownSetup}
      stName={setup.name}
      chosen={props.chosen}
      setChosenStID={props.setChosenStID}
      setModalType={props.setModalType}
      setImportedSt={props.setImportedSt}
      setSharedID={props.setSharedID}
      uncombine={uncombine}
      buildTmSt={props.buildTmSt}
      setAddingID={props.setAddingID}
      allIDs={allIDs}
      switchMember={switchMember}
      result={props.result}
    />
  );
}
