import { useState, useContext, useCallback } from "react";
import { SetDBCt } from "../../../../App";
import { Card } from "../../../../styledCpns/Database";
import { Box, HiddenSpace } from "../../../../styledCpns/Layout";
import { CloseBtn } from "../../../CommonCpns";
import WpInventory from "../../../Readers/WpInventory";
import ArtInventory from "../../../Readers/ArtInventory";
import { SharedSpace } from "../../../Readers/styles";
import Overview from "./Overview";
import Details from "./Details";
import { artTypes } from "../../../../data/misc";
import { deepCopy } from "../../../../helpers/misc";
import { useGetHeight } from "../../../../helpers/hooks";

export default function PreCheck(props) {
  const { owner, ownerIndex, weapon, art, artIDs } = props;
  const { setMyChars, setMyWps, setMyArts } = useContext(SetDBCt);
  const [opened, setOpened] = useState(-1);
  const [showing, setShowing] = useState(false);
  const [wpInvOn, setWpInvOn] = useState(false);
  const [artInvI, setArtInvI] = useState(-1);

  const toggleDetails = useCallback(
    (type) => {
      if (opened === type) {
        setShowing(false);
        setTimeout(() => setOpened(-1), 200);
      } else {
        setShowing(true);
        setOpened(type);
      }
    },
    [opened]
  );

  const [ref, height] = useGetHeight();
  return (
    <div ref={ref} className="ml-2">
      {window.innerWidth < 686 ? (
        <SectionGearsSmall
          {...props}
          opened={opened}
          setOpened={setOpened}
          showing={showing}
          setShowing={setShowing}
          toggleDetails={toggleDetails}
          setWpInvOn={setWpInvOn}
          setArtInvI={setArtInvI}
          height={height}
        />
      ) : (
        <SectionGears
          {...props}
          opened={opened}
          setOpened={setOpened}
          showing={showing}
          setShowing={setShowing}
          toggleDetails={toggleDetails}
          setWpInvOn={setWpInvOn}
          setArtInvI={setArtInvI}
          height={height}
        />
      )}
      {wpInvOn && (
        <WpInventory
          owner={owner}
          wpType={weapon.type}
          close={() => setWpInvOn(false)}
          btnText="Switch"
          btnClick={(targetWp) => {
            setMyWps((prev) => {
              const newWps = deepCopy(prev);
              newWps[
                newWps.findIndex((wp) => wp.ID === targetWp.ID)
              ].user = owner;
              newWps[props.weaponI].user = targetWp.user;
              return newWps;
            });
            setMyChars((prev) => {
              const newChars = deepCopy(prev);
              newChars[ownerIndex].weaponID = targetWp.ID;
              if (targetWp.user) {
                newChars[
                  newChars.findIndex((char) => char.name === targetWp.user)
                ].weaponID = weapon.ID;
              }
              return newChars;
            });
          }}
        />
      )}
      {artInvI >= 0 && (
        <ArtInventory
          curPieces={art.pieces}
          artType={artTypes[artInvI]}
          owner={owner}
          close={() => setArtInvI(-1)}
          btnText="Switch"
          btnClick={(targetArt) => {
            setMyArts((prev) => {
              const newArts = deepCopy(prev);
              newArts[
                newArts.findIndex((art) => art.ID === targetArt.ID)
              ].user = owner;
              const index = newArts.findIndex(
                (art) => art.ID === artIDs[artInvI]
              );
              if (index !== -1) newArts[index].user = targetArt.user;
              return newArts;
            });
            setMyChars((prev) => {
              const newChars = deepCopy(prev);
              newChars[ownerIndex].artIDs[artInvI] = targetArt.ID;
              if (targetArt.user) {
                for (let unit of newChars) {
                  if (unit.name === targetArt.user) {
                    unit.artIDs[artInvI] = artIDs[artInvI];
                    break;
                  }
                }
              }
              return newChars;
            });
          }}
        />
      )}
    </div>
  );
}

function SectionGearsSmall(props) {
  const { weapon, art, setArtInvI, opened, showing, toggleDetails } = props;
  return (
    <Card className="full-h" width="20.75rem">
      <Box className="full-h">
        <SharedSpace>
          <div className={"left_part " + (showing ? "close" : "open")}>
            <Overview
              weapon={weapon}
              art={art}
              opened={opened}
              toggleDetails={toggleDetails}
              setArtInvI={setArtInvI}
            />
          </div>
          <div className={"right_part " + (showing ? "open" : "close")}>
            <div className="full-h flex-col">
              <CloseBtn
                className="mx-auto"
                onClick={() => toggleDetails(opened)}
              />
              {opened !== -1 && (
                <Details
                  height={props.height}
                  opened={opened}
                  setOpened={props.setOpened}
                  weapon={weapon}
                  setWpInvOn={props.setWpInvOn}
                  art={art}
                  openArtInv={() => setArtInvI(opened)}
                  ownerIndex={props.ownerIndex}
                  artSBnes={props.artSBnes}
                  setShowing={props.setShowing}
                  close={() => toggleDetails(opened)}
                />
              )}
            </div>
          </div>
        </SharedSpace>
      </Box>
    </Card>
  );
}

function SectionGears(props) {
  const { weapon, art, setArtInvI, opened, toggleDetails } = props;
  return (
    <div className="full-h flex">
      <Card width="20.75rem">
        <Overview
          weapon={weapon}
          art={art}
          opened={opened}
          toggleDetails={toggleDetails}
          setArtInvI={setArtInvI}
        />
      </Card>
      <HiddenSpace
        className={"hide-sb " + (props.showing ? "show" : "")}
        openWidth="20.375rem"
      >
        {opened !== -1 && (
          <Details
            large
            height={props.height}
            opened={opened}
            setOpened={props.setOpened}
            weapon={weapon}
            setWpInvOn={props.setWpInvOn}
            art={art}
            openArtInv={() => setArtInvI(opened)}
            ownerIndex={props.ownerIndex}
            artSBnes={props.artSBnes}
            setShowing={props.setShowing}
            close={() => toggleDetails(opened)}
          />
        )}
      </HiddenSpace>
    </div>
  );
}
