import { useCallback, useState } from "react";
import artifacts from "../../data/artifacts";
import { StyledRack } from "../../styledCpns/Database";
import { NoItems } from "../CommonCpns";
import Thumb from "../Readers/Thumb";

export default function LeftSection(props) {
  return (
    <StyledRack className="hide-sb">
      {props.arts.length ? <Items {...props} /> : <NoItems type="Artifacts" />}
    </StyledRack>
  );
}

function Items({ chosenID, setChosenID, arts }) {
  const [clickedI, setClickedI] = useState(null);
  const choose = useCallback(
    (id) => {
      setClickedI(null);
      setChosenID(id);
    },
    [setChosenID]
  );
  return (
    <div className="flex-wrap">
      {arts.map(({ set, type, ID, user, rarity, level }, i) => {
        const { beta } = artifacts[set];
        const { icon } = artifacts[set][type];
        return (
          <div key={ID} className="cell relative">
            <Thumb
              index={i}
              item={{ beta, ID, user, rarity, level, icon }}
              chosen={ID === chosenID}
              clicked={i === clickedI}
              handleMouseDown={setClickedI}
              choose={choose}
            />
          </div>
        );
      })}
    </div>
  );
}
