import characters from "../../data/characters";
import weapons from "../../data/weapons";
import artifactBuffs from "../../data/artifacts/artifactBuffs";
import monsters from "../../data/monsters";
import { elmts } from "../../data/elements";

// [./CalcChar/index, ./SetupManager/SectionParty, ./SetupManager/Settings/helpers,
// ../MySetups/helpers, decode, ../../helpers/processors]
export function initCharMCs(charName, forSelf) {
  const result = { BCs: [], DCs: [] };
  const { buffs, debuffs } = characters[charName];
  if (buffs) {
    for (const buff of buffs) {
      if (!buff.outdated && buff.affect !== (forSelf ? "teammates" : "self")) {
        const node = { activated: false, index: buff.index };
        if ((forSelf && buff.selfLabels) || (!forSelf && buff.labels)) {
          node.inputs = [...buff.inputs];
        }
        result.BCs.push(node);
      }
    }
  }
  // v2.4 - debuffs need inputs:
  // From self: AnemoMC C6 & Venti C6
  // Not from self: Eula E & Shenhe Q
  function getInputs(labels, inputTypes) {
    const inputs = [];
    labels.forEach((_, i) => {
      const type = inputTypes[i];
      if (type === "absorption") {
        inputs.push("Pyro");
      } else if (type === "text") {
        // level
        inputs.push(1);
      }
    });
    return inputs;
  }
  if (debuffs) {
    for (const debuff of debuffs) {
      if (debuff.outdated || (!forSelf && debuff.affect === "self")) {
        continue;
      }
      const node = { activated: false, index: debuff.index };
      const { selfLabels, labels, inputTypes } = debuff;
      if (forSelf && selfLabels) {
        node.inputs = getInputs(selfLabels, inputTypes);
      } else if (!forSelf && labels) {
        node.inputs = getInputs(labels, inputTypes);
      }
      result.DCs.push(node);
    }
  }
  return result;
}

// [./CalcChar/index, ./SetupManager/Settings/helpers]
export function initElmtMCs() {
  return {
    naAmpRxn: null,
    ampRxn: null,
    superconduct: false,
    resonance: []
  };
}

// [./CalcChar/helpers, ./SetupManager/index, ./SetupManager/Settings/helpers,
// ./SetupManager/SectionWp, decode, ../../helpers/processors]
export function getMainWpBCs(type, name) {
  const result = [];
  const { buffs } = weapons[type][name];
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "stacks") {
              node.inputs.push(1);
            } else if (type === "check") {
              node.inputs.push(false);
            } else if (type === "choices") {
              node.inputs.push(buff.values[j][0]);
            }
          });
        }
        result.push(node);
      }
    });
  }
  return result;
}

// [here, ./SetupManager/SectionWp]
export function getBCsOfWp(wpType, wpName) {
  const BCs = [];
  const { buffs } = weapons[wpType][wpName];
  if (buffs) {
    buffs.forEach((buff, index) => {
      if (buff.affect !== "self") {
        const node = { name: wpName, activated: false, refinement: 1, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type, j) => {
            if (type === "choices") {
              node.inputs.push(buff.values[j][0]);
            }
          });
        }
        BCs.push(node);
      }
    });
  }
  return BCs;
}

// [./CalcChar/index, ./SetupManager/Settings/helpers]
export function initSubWpMCs() {
  return { BCs: {}, DCs: {} };
}

// [./SetupManager/SectionParty, ../MySetups/helpers, decode,
// ../../helpers/processors]
export function getSubWpBCs(wpType, mainWpName) {
  const result = [];
  for (const name in weapons[wpType]) {
    if (name !== mainWpName) {
      result.push(...getBCsOfWp(wpType, name));
    }
  }
  return result;
}

// [./CalcChar/helpers, ./SetupManager/SectionArts, decode,
// ../../helpers/processors]
export function getMainArtBCs(setName) {
  const result = [];
  if (artifactBuffs[setName]) {
    artifactBuffs[setName].forEach((buff, index) => {
      if (buff.affect !== "teammates") {
        const node = { activated: false, index };
        if (buff.inputTypes) {
          node.inputs = [];
          buff.inputTypes.forEach((type) => {
            if (type === "stacks") {
              node.inputs.push(buff.startsAt0 ? 0 : 1);
            } else if (type === "crystallize") {
              node.inputs.push("Pyro");
            }
          });
        }
        result.push(node);
      }
    });
  }
  return result;
}

// [./CalcChar/helpers, ./SetupManager/Settings/helpers, decode,
// ../../helpers/processors]
// related to ./SetupManager/SectionArts => setArtPiece()
export function getSubArtBCs(mainSet) {
  const result = [];
  for (const set in artifactBuffs) {
    if (set !== mainSet) {
      artifactBuffs[set].forEach((buff, index) => {
        if (buff.affect !== "self") {
          const node = { set, activated: false, index };
          if (buff.inputTypes) {
            node.inputs = [];
            buff.inputTypes.forEach((type) => {
              if (type === "crystallize") {
                node.inputs.push("Pyro");
              }
            });
          }
          result.push(node);
        }
      });
    }
  }
  return result;
}

// [./CalcChar/helpers, ./SetupManager/Settings/helpers, decode,
// ../../helpers/processors]
export function getSubArtDCs() {
  return [
    { set: "Viridescent Venerer", activated: false, index: 0, inputs: ["Pyro"] }
  ];
}

// [./CalcChar/index, ./SetupManager/Settings/helpers, decode]
export function initParty() {
  return [null, null, null];
}

export function initTarget() {
  const result = { Level: 1, "Physical RES": 10 };
  for (const elmt of elmts) {
    result[elmt + " RES"] = 10;
  }
  return result;
}

// [./index, ./CalcChar/index]
export function initMonster() {
  return {
    name: monsters[0].name,
    inputs: []
  };
}

// [./SetupManager/SectionParty, decode, ../../helpers/processors]
export function getVisionCount(char, party) {
  const count = {};
  count[characters[char.name].vision] = 1;
  for (const tm of party) {
    if (tm) {
      const { vision } = characters[tm.name];
      count[vision] = (count[vision] || 0) + 1;
    }
  }
  return count;
}

// [./SetupManager/SectionParty, ../MySetups/helpers, decode,
// ../../helpers/processors]
export function getWpRange(party) {
  const result = {};
  for (const tm of party) {
    if (tm) result[characters[tm.name].weapon] = 1;
  }
  return result;
}

// [here, ./CalcChar/index, ./SetupManager/Settings/index & SetupCtrl]
export function getSetupCtrl({
  name = "Setup 1",
  ID = Date.now(),
  type = "original"
}) {
  return { name, ID, type };
}

// [here, ./SetupManager/Settings/index]
export function getNewSetupName(setups) {
  const existedIs = [1, 2, 3, 4];
  for (const st of setups) {
    const parts = st.name.split(" ");
    if (parts.length === 2 && parts[0] === "Setup" && !isNaN(parts[1])) {
      const i = existedIs.indexOf(+parts[1]);
      if (i !== -1) existedIs.splice(i, 1);
    }
  }
  return "Setup " + existedIs[0];
}

export function initSession(importedSt, CBs) {
  CBs.setChar(importedSt.char);
  CBs.setSelfMCs([importedSt.selfMCs]);
  CBs.setWpRack([importedSt.weapon]);
  CBs.setSubWpMCs([importedSt.subWpMCs]);
  CBs.setArtChest([importedSt.art]);
  CBs.setParties([importedSt.party]);
  CBs.setElmtMCs([importedSt.elmtMCs]);
  const { ID, type } = importedSt;
  CBs.setSetups([getSetupCtrl({ ID, type })]);
  CBs.setTarget(importedSt.target);
  CBs.setStdSetupI(0);
  CBs.setCurSetupI(0);
  CBs.setCmpSetupIs([0]);
  CBs.setSettingsOn(false);
  CBs.toCalc();
}

export function addSetup(importedSt, overrideChar, overrideTg, length, CBs) {
  if (overrideChar) CBs.setChar(importedSt.char);
  if (overrideTg) CBs.setTarget(importedSt.target);
  CBs.setSelfMCs((prev) => [...prev, importedSt.selfMCs]);
  CBs.setWpRack((prev) => [...prev, importedSt.weapon]);
  CBs.setSubWpMCs((prev) => [...prev, importedSt.subWpMCs]);
  CBs.setArtChest((prev) => [...prev, importedSt.art]);
  CBs.setParties((prev) => [...prev, importedSt.party]);
  CBs.setElmtMCs((prev) => [...prev, importedSt.elmtMCs]);
  const { ID, type } = importedSt;
  CBs.setSetups((prev) => [
    ...prev,
    getSetupCtrl({ name: getNewSetupName(prev), ID, type })
  ]);
  CBs.setCurSetupI(length);
  CBs.setSettingsOn(false);
  CBs.toCalc();
}
