import { useState } from "react";
import { Text, LGreen } from "../../styledCpns/DataDisplay";
import { Box, AbsoluteBox } from "../../styledCpns/Layout";
import { Select, IconBtn } from "../../styledCpns/Inputs";
import { deepCopy } from "../misc";
import { elmtDmgBnes } from "../../data/misc";
import { artMS, artSSTypes } from "../../data/artifacts/config";

const mainSTypes = [
  "All",
  "HP",
  "HP%",
  "ATK",
  "ATK%",
  "DEF%",
  "CRIT Rate",
  "CRIT DMG",
  "Elemental Mastery",
  "Energy Recharge",
  "Healing Bonus",
  "Physical DMG Bonus",
  ...elmtDmgBnes
];

export default function useArtStatsFilter(stats, err, artType) {
  const [tempStats, setTempStats] = useState(deepCopy(stats));
  const [atInfo, setAtInfo] = useState(false);
  const statsFilter = (
    <Box
      className="mr-2 px-3 py-2 full-h relative"
      width="17.5rem"
      borderRadius="0.5rem"
      bgColor="cardBg2"
    >
      <AbsoluteBox top="18.3rem" left="0.75rem">
        <IconBtn
          className="glow-on-hover"
          icon={"fas fa-" + (atInfo ? "times" : "info")}
          variant={atInfo ? "negative" : "default"}
          size="1.5rem"
          fontSize="0.875rem"
          onClick={() => setAtInfo(!atInfo)}
        />
      </AbsoluteBox>
      {atInfo ? (
        <div className="mt-2">
          <Text>
            Artifacts will not only be <LGreen>filtered</LGreen> by stats, but
            also be <LGreen>sorted</LGreen> by stats. The priority is Main Stat
            (if it's not "All"), then Sub Stat 1, Sub Stat 2...
          </Text>
        </div>
      ) : (
        <WorkMode
          artType={artType}
          tempStats={tempStats}
          setTempStats={setTempStats}
          err={err}
        />
      )}
    </Box>
  );
  return [tempStats, statsFilter, setTempStats];
}

function WorkMode(props) {
  const {
    artType,
    tempStats: { main, subs },
    setTempStats
  } = props;
  return (
    <div className="full-h flex-col">
      <Text className="mt-2" variant="h6" color="orange" fontWeight={700}>
        Main Stat
      </Text>
      <div className="mt-1 flex justify-center">
        <Box className="px-3" bgColor="cardBg1" width="13.25rem">
          <Select
            className="p-1"
            color={main === "All" ? "white" : "green"}
            align="center"
            value={main}
            onChange={(e) =>
              setTempStats((prev) => ({ ...prev, main: e.target.value }))
            }
          >
            {(artType
              ? ["All", ...Object.keys(artMS[artType])]
              : mainSTypes
            ).map((type, i) => (
              <option key={i}>{type}</option>
            ))}
          </Select>
        </Box>
      </div>
      <Text className="mt-2" variant="h6" color="orange" fontWeight={700}>
        Sub Stats
      </Text>
      <div className="flex-col align-center">
        {[1, 2, 3, 4].map((n, i) => (
          <SSLine
            key={i}
            n={n}
            i={i}
            subs={subs}
            setSS={(e) =>
              setTempStats((prev) => {
                const st = deepCopy(prev);
                const { value } = e.target;
                st.subs[i] = value;
                if (value === "All") {
                  for (let k = i; k < 4; k++) {
                    st.subs[k] = "All";
                  }
                }
                return st;
              })
            }
          />
        ))}
      </div>
      {props.err && (
        <Text className="mt-3" color="lightRed" align="right">
          Every stat must be unique.
        </Text>
      )}
    </div>
  );
}

function SSLine({ n, i, subs, setSS }) {
  return (
    <Box
      className="mt-2 px-3 flex align-center"
      width="13.25rem"
      height="2rem"
      bgColor="cardBg1"
    >
      <Text className="mr-1 mt-1" color="orange">
        {n}
      </Text>
      {(!i || subs[i - 1] !== "All") && (
        <Select
          className="p-1"
          color={subs[i] === "All" ? "white" : "green"}
          align="center"
          value={subs[i]}
          onChange={setSS}
        >
          {["All", ...artSSTypes].map((type, j) => (
            <option key={j}>{type}</option>
          ))}
        </Select>
      )}
    </Box>
  );
}
