import { useCallback, useContext, useState } from "react";
import { MyCharsCt, MyWpsCt, SetDBCt } from "../../App";
import characters from "../../data/characters";
import { useGetHeight } from "../../helpers/hooks";
import { deepCopy } from "../../helpers/misc";
import { Card } from "../../styledCpns/Database";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, Confirmation, EquippedChar } from "../CommonCpns";
import Picker from "../Picker";
import { initWeapon } from "../Picker/ItemPicker";
import { MyWpCard } from "../Readers/MyItemCard";

export default function RightSection({ weapon, setChosenID }) {
  const [pickerOn, setPickerOn] = useState(null);
  const [removing, setRemoving] = useState(false);
  const [ref, height] = useGetHeight();
  return (
    <div ref={ref} className="full-h flex-col">
      <div className="grow-1 flex align-start">
        <Card
          className="flex-col"
          style={{ minHeight: "27rem", maxHeight: height / 16 - 3 + "rem" }}
        >
          <Box className="grow-1 hide-sb" width="18.75rem">
            {weapon ? <MyWpCard weapon={weapon} mutable={true} /> : null}
          </Box>
          {weapon ? (
            <ButtonBar
              className="mt-3"
              texts={["Remove", "Equip"]}
              handlers={[() => setRemoving(true), () => setPickerOn(true)]}
            />
          ) : null}
        </Card>
      </div>
      {weapon?.user ? <EquippedChar user={weapon.user} /> : null}
      {pickerOn && (
        <CharPicker weapon={weapon} close={() => setPickerOn(false)} />
      )}
      {removing && (
        <ConfirmRemove
          weapon={weapon}
          setChosenID={setChosenID}
          close={() => setRemoving(false)}
        />
      )}
    </div>
  );
}

function CharPicker({ weapon: { ID, user, type }, close }) {
  const myChars = useContext(MyCharsCt);
  const { setMyChars, setMyWps } = useContext(SetDBCt);
  const data = [];
  for (let { name, constellation } of myChars) {
    if (characters[name].weapon === type && name !== user) {
      const { code, icon, rarity, vision, weapon } = characters[name];
      data.push({ code, name, icon, rarity, vision, weapon, constellation });
    }
  }
  const swap = useCallback((name) => {
    const targetCharI = myChars.findIndex((char) => char.name === name);
    setMyWps((prev) => {
      const wps = deepCopy(prev);
      const wpI_new = wps.findIndex((item) => item.ID === ID);
      wps[wpI_new].user = name;
      const { weaponID } = myChars[targetCharI];
      if (weaponID) {
        const wpI_old = wps.findIndex((item) => item.ID === weaponID);
        wps[wpI_old].user = user;
      }
      return wps;
    });
    setMyChars((prev) => {
      const chars = deepCopy(prev);
      if (user) {
        const currentCharI = chars.findIndex((char) => char.name === user);
        chars[currentCharI].weaponID = chars[targetCharI].weaponID;
      }
      chars[targetCharI].weaponID = ID;
      return chars;
    });
  }, []);

  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) => swap(name)}
    />
  );
}

function ConfirmRemove({
  weapon: { ID, user, name, type },
  setChosenID,
  close
}) {
  const { setMyChars, setMyWps } = useContext(SetDBCt);
  const myWps = useContext(MyWpsCt);
  const index = myWps.findIndex((item) => item.ID === ID);
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{name}</b>
          "?{" "}
          {Boolean(user) && (
            <>
              It is currently used by <b>{user}</b>.
            </>
          )}
        </>
      }
      close={close}
      confirm={() => {
        const newWpID = Date.now();
        setMyWps((prev) => {
          const wps = deepCopy(prev);
          wps.splice(index, 1);
          if (myWps.length > 1) {
            const newIndex = index + (index === myWps.length - 1 ? -1 : 1);
            setChosenID(myWps[newIndex].ID);
          }
          if (user) {
            wps.push({
              ID: newWpID,
              user,
              ...initWeapon({ type })
            });
          }
          return wps;
        });
        if (user) {
          setMyChars((prev) => {
            const chars = deepCopy(prev);
            chars[
              chars.findIndex((char) => char.name === user)
            ].weaponID = newWpID;
            return chars;
          });
        }
      }}
    />
  );
}
