import characters from "../../../data/characters";
import {
  getMainWpBCs,
  getMainArtBCs,
  getSubArtBCs,
  getSubArtDCs
} from "../helpers";
import { getArtSets } from "../../../helpers/processors";
import { initWeapon } from "../../Picker/ItemPicker";

export function parseData(unit, myWeapons, myArts) {
  const { name, weaponID, artIDs = [null, null, null, null, null] } = unit;
  const char = {
    name,
    level: unit.level || "1/20",
    ascension: unit.ascension || 0,
    "Normal Attack": unit["Normal Attack"] || 1,
    "Elemental Skill": unit["Elemental Skill"] || 1,
    "Elemental Burst": unit["Elemental Burst"] || 1,
    constellation: unit.constellation || 0
  };

  let wp;
  if (weaponID) {
    const existedWp = myWeapons.find((wp) => wp.ID === weaponID);
    if (existedWp) {
      const { ID, user, ...weapon } = existedWp;
      wp = {
        ...weapon,
        BCs: getMainWpBCs(existedWp.type, existedWp.name)
      };
    }
  } else {
    const newWp = initWeapon({ type: characters[name].weapon });
    wp = { ...newWp, BCs: getMainWpBCs(newWp.type, newWp.name) };
  }

  const pieces = artIDs.map((id) => {
    const artP = myArts.find((p) => p.ID === id);
    if (artP) {
      const { user, ...info } = artP;
      return info;
    }
    return null;
  });
  const sets = getArtSets(pieces);
  const setName = sets[0]?.bonusLv === 1 ? sets[0].name : null;
  const art = {
    pieces,
    sets,
    BCs: getMainArtBCs(setName),
    subBCs: getSubArtBCs(setName),
    subDCs: getSubArtDCs()
  };
  return { char, wp, art };
}

export function getMixedCharList(myChars) {
  const result = [];
  for (let name in characters) {
    const { code, beta, icon, rarity, vision, weapon } = characters[name];
    const char = { code, beta, icon, rarity, vision, weapon };
    const existedUnit = myChars.find((unit) => unit.name === name);
    if (existedUnit) {
      result.push({ ...existedUnit, ...char });
    } else {
      result.push({ name, ...char });
    }
  }
  return result;
}
