import { useEffect } from "react";
import { Text } from "../../../styledCpns/DataDisplay";
import { Box, AbsoluteBox } from "../../../styledCpns/Layout";
import { CloseBtn } from "../../CommonCpns";
import { TalentInfo, SkillAttributes } from "./DetailsContent";
import { SlideShow } from "../styles";
import { useSwitcher } from "../../../helpers/hooks";

export default function Details(props) {
  const { index, setIndex } = props;
  const { code, actvTalents, pasvTalents, vision } = props.charData;
  const isActv = index < actvTalents.length;
  let { type, name, desc } = isActv
    ? actvTalents[index]
    : pasvTalents[index - actvTalents.length];
  const [switcher, tab, setTab] = useSwitcher([
    { text: "Talent Info", clickable: true },
    { text: "Skill Attributes", clickable: isActv }
  ]);

  useEffect(() => {
    if (!isActv && tab === "Skill Attributes") {
      setTab("Talent Info");
    }
  }, [isActv, tab, setTab]);

  useEffect(() => {
    setTab("Talent Info");
  }, [code, setTab]);

  return (
    <div className="full-h flex-col relative">
      <div className="hide-sb">
        <SlideShow
          forTl
          index={index}
          images={[...actvTalents, ...pasvTalents].map((tl) => tl.image)}
          vision={vision}
          back={() => setIndex(index - 1)}
          next={() => setIndex(index + 1)}
          typeCpn={
            <AbsoluteBox className="full-w" top={0}>
              <Box width="25%">
                <Text variant="subtitle1">
                  {type}
                  {!isNaN(type.slice(-1)) && " Passive"}
                </Text>
              </Box>
            </AbsoluteBox>
          }
        />
        <Text variant="h5" color={vision} fontWeight={700} align="center">
          {name}
        </Text>
        <div className="mt-2">{switcher}</div>
        {tab === "Talent Info" ? (
          <TalentInfo desc={desc} isActv={isActv} />
        ) : (
          <SkillAttributes
            tlData={actvTalents[index]}
            isActv={isActv}
            code={code}
          />
        )}
      </div>
      <div className="mt-3">
        <CloseBtn className="mx-auto glow-on-hover" onClick={props.close} />
      </div>
    </div>
  );
}
