import { useContext } from "react";
import { MyCharsCt, MyWpsCt, MyArtsCt, SetDBCt } from "../../../App";
import { Box } from "../../../styledCpns/Layout";
import { Card } from "../../../styledCpns/Database";
import ConsList from "../../Readers/ConsList";
import TalentList from "../../Readers/TalentList";
import SectionAttr from "./SectionAttr";
import SectionGears from "./SectionGears";
import characters from "../../../data/characters";
import { getArtSets } from "../../../helpers/processors";
import { deepCopy } from "../../../helpers/misc";
import getBaseStats from "../../../calculators/baseStats";

export default function Info({ charIndex, setChosenI }) {
  const myChars = useContext(MyCharsCt);
  const myWeapons = useContext(MyWpsCt);
  const myArtifacts = useContext(MyArtsCt);
  const { setMyChars } = useContext(SetDBCt);
  const { weaponID, artIDs, ...char } = myChars[charIndex];
  const charData = characters[char.name];
  const [weapon, weaponI] = getWpInfo(weaponID, myWeapons);
  const art = getArtifact(artIDs, myArtifacts);
  const [ATTRs, artSBnes] = getBaseStats(charData, char, weapon, art);

  return (
    <Box
      className="py-3 flex overflow-auto"
      width={window.innerWidth <= 480 ? "calc(100% - 2rem)" : "88%"}
      height="98%"
    >
      <SectionAttr
        charData={charData}
        char={char}
        charI={charIndex}
        ATTRs={ATTRs}
        setChosenI={setChosenI}
      />
      <SectionGears
        owner={char.name}
        ownerIndex={charIndex}
        weapon={weapon}
        weaponI={weaponI}
        art={art}
        artIDs={artIDs}
        artSBnes={artSBnes}
      />
      <Card className="ml-2">
        <Box className="full-h" width="18.75rem">
          <ConsList
            name={char.name}
            constellation={char.constellation}
            handleClick={(i) =>
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                const { constellation } = newChars[charIndex];
                newChars[charIndex].constellation =
                  constellation === i + 1 ? i : i + 1;
                return newChars;
              })
            }
          />
        </Box>
      </Card>
      <Card className="ml-2">
        <Box className="full-h" width="18.75rem">
          <TalentList
            char={char}
            upgradeTl={(type, level) =>
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                newChars[charIndex][type] = level;
                return newChars;
              })
            }
          />
        </Box>
      </Card>
    </Box>
  );
}

function getWpInfo(weaponID, myWeapons) {
  for (let i in myWeapons) {
    if (myWeapons[i].ID === weaponID) {
      return [myWeapons[i], i];
    }
  }
  return [];
}

function getArtifact(artIDs, myArtifacts) {
  const pieces = artIDs.map(
    (id) => myArtifacts.find((p) => p.ID === id) || null
  );
  return { pieces, sets: getArtSets(pieces) };
}
