import { useState, memo } from "react";
import { ExpandableList } from "../../CommonCpns";
import { Card, MainSelect } from "../styles";
import { SelfBuffs, PartyBuffs } from "./Buffs/CharBuffs";
import { ElmtBuffs, ArtBuffs } from "./Buffs/OtherBuffs";
import WpBuffs from "./Buffs/WpBuffs";
import { SelfDebuffs, PartyDebuffs } from "./Debuffs/CharDebuffs";
import { ElmtDebuffs, ArtDebuffs } from "./Debuffs/OtherDebuffs";
import Target from "./Target";

export default memo(Modifiers);

function Modifiers() {
  const [tab, setTab] = useState("Buffs");
  return (
    <Card className="px-4 py-3 flex-col" bgColor="cardBg1">
      <MainSelect
        tab={tab}
        setTab={setTab}
        options={["Buffs", "Debuffs", "Target"]}
      />
      <div className="mt-3 grow-1 custom-sb">
        {tab === "Buffs" ? (
          <ExpandableList
            headingList={[
              "Resonance & Reactions",
              "Self",
              "Party",
              "Weapons",
              "Artifacts"
            ]}
            contentList={[
              <ElmtBuffs />,
              <SelfBuffs />,
              <PartyBuffs />,
              <WpBuffs />,
              <ArtBuffs />
            ]}
          />
        ) : tab === "Debuffs" ? (
          <ExpandableList
            headingList={[
              "Resonance & Reactions",
              "Self",
              "Party",
              "Artifacts"
            ]}
            contentList={[
              <ElmtDebuffs />,
              <SelfDebuffs />,
              <PartyDebuffs />,
              <ArtDebuffs />
            ]}
          />
        ) : (
          <Target />
        )}
      </div>
    </Card>
  );
}
