import { useContext, useState } from "react";
import { MyArtsCt, SetDBCt } from "../../../App";
import { artMS } from "../../../data/artifacts/config";
import { deepCopy, pctOf } from "../../../helpers/misc";
import { processNumInp } from "../../../helpers/processors";
import { Text } from "../../../styledCpns/DataDisplay";
import { IconBtn, Select } from "../../../styledCpns/Inputs";
import { Box } from "../../../styledCpns/Layout";
import { ButtonBar, Confirmation } from "../../CommonCpns";
import { Collapse } from "../../CommonCpns/HiddenSpace";
import ArtSubstats from "../../Readers/ArtifactCard/ArtSubstats";
import { FixedCts, SetupCt } from "../index";
import { setArtPiece } from "./SectionArts";

export default function ArtPieceInfo({ infoOn, pieces, tabNo, setPickerOn }) {
  return (
    <Collapse in={infoOn}>
      {pieces[tabNo] ? (
        <FilledCard
          artP={pieces[tabNo]}
          tabNo={tabNo}
          infoOn={infoOn}
          setPickerOn={setPickerOn}
        />
      ) : (
        <EmptyCard setPickerOn={setPickerOn} />
      )}
    </Collapse>
  );
}

export function FilledCard({ artP, tabNo, infoOn, setPickerOn }) {
  const { rarity = 5 } = artP;
  const msType = artP.mainSType;
  const availableMSTypes = artMS[artP.type];
  const isFoP = ["flower", "plume"].includes(artP.type);
  const msValues = availableMSTypes[msType][rarity];
  const [modal, setModal] = useState(0);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const myArts = useContext(MyArtsCt);
  const { setMyArts } = useContext(SetDBCt);

  const enhance = (e) =>
    setArtChest((prev) => {
      const chest = deepCopy(prev);
      chest[curSetupI].pieces[tabNo].level = +e.target.value.substr(1);
      return chest;
    });
  const changeMSType = (e) =>
    setArtChest((prev) => {
      const chest = deepCopy(prev);
      chest[curSetupI].pieces[tabNo].mainSType = e.target.value;
      return chest;
    });
  const changeSSType = (value, ssIndex) =>
    setArtChest((prev) => {
      const chest = deepCopy(prev);
      chest[curSetupI].pieces[tabNo].subS[ssIndex].type = value;
      return chest;
    });
  const changeSSVal = (value, ssIndex, oldValue) =>
    setArtChest((prev) => {
      const chest = deepCopy(prev);
      chest[curSetupI].pieces[tabNo].subS[ssIndex].val = processNumInp(
        value,
        oldValue,
        9999
      );
      return chest;
    });
  const remove = () => setArtPiece(curSetupI, tabNo, null, setArtChest);
  const save = () => {
    if (!myArts.some(({ ID }) => ID === artP.ID)) {
      setMyArts((prev) => {
        const newArts = [...prev];
        newArts.unshift({ user: null, ...artP });
        return newArts;
      });
      setModal(1);
    } else {
      setModal(2);
    }
  };
  const switchArtP = () => setPickerOn(true);
  return (
    <div className="pt-4">
      <div className="pl-4 flex align-start">
        <Box bgColor="cardBg3" borderRadius="50%">
          <Select
            color={rarity}
            fontSize="1.125rem"
            fontWeight={700}
            className="appearance-none px-2 pt-2 pb-1 pointer"
            value={"+" + artP.level}
            onChange={enhance}
          >
            {[...Array(rarity === 5 ? 21 : 17).keys()].map((_, lv) => (
              <option key={lv}>+{lv}</option>
            ))}
          </Select>
        </Box>
        <div className="ml-3">
          {isFoP ? (
            <Text className="pl-5 pt-1" variant="h6">
              {msType}
            </Text>
          ) : (
            <div className="py-1">
              <Select
                fontSize="1.125rem"
                color="white"
                className="pl-5 appearance-none bg-for-icon white-arrow-bg"
                value={msType}
                onChange={changeMSType}
              >
                {Object.keys(availableMSTypes).map((type) => (
                  <option key={type}>{type}</option>
                ))}
              </Select>
            </div>
          )}
          <Text className="pl-5" variant="h3" color={rarity}>
            {msValues[artP.level] + pctOf(msType)}
          </Text>
        </div>
      </div>
      <div className="px-2 pb-1">
        <ArtSubstats
          mutable={true}
          substats={artP.subS}
          changeSSType={changeSSType}
          changeSSVal={changeSSVal}
          msType={msType}
          rarity={rarity}
        />
      </div>
      <div className="mt-3 flex justify-evenly align-center">
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-trash-alt"
          variant="negative"
          onClick={remove}
        />
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-save"
          variant="neutral"
          onClick={save}
        />
        <IconBtn
          className="glow-on-hover"
          icon="fas fa-sync-alt"
          variant="positive"
          fontSize="1.25rem"
          onClick={switchArtP}
        />
      </div>
      {modal !== 0 && (
        <ConfirmSave modal={modal} artP={artP} close={() => setModal(0)} />
      )}
    </div>
  );
}

function ConfirmSave({ modal, artP, close }) {
  const myArts = useContext(MyArtsCt);
  const { setMyArts } = useContext(SetDBCt);
  const index = myArts.findIndex(({ ID }) => ID === artP.ID);
  const { user } = myArts[index];
  let message;
  if (modal === 1) {
    message = "Successfully saved to My Artifacts.";
  } else {
    message = (
      <>
        This one is already in My Artifacts
        {user ? (
          <>
            , currently used by <b>{user}</b>.
          </>
        ) : (
          "."
        )}{" "}
        Do you want to overwrite its Stats?
      </>
    );
  }
  return (
    <Confirmation
      message={message}
      otherBtn={modal === 1 ? { text: "Close" } : null}
      confirm={() => {
        if (modal === 1) {
          close();
        } else {
          setMyArts((prev) => {
            const newArts = deepCopy(prev);
            const { ID, ...stats } = artP;
            newArts[index] = { ...newArts[index], ...stats };
            return newArts;
          });
        }
      }}
      close={close}
    />
  );
}

export function EmptyCard({ setPickerOn }) {
  return (
    <Box height="18.05rem">
      <div className="py-4 flex-center">
        <Text variant="h6">Not equipping any artifact</Text>
      </div>
      <ButtonBar
        texts={["Switch"]}
        variants={["positive"]}
        handlers={[() => setPickerOn(true)]}
      />
    </Box>
  );
}
