import styled from "styled-components";
import { useContext, useEffect } from "react";
import { SetDBCt } from "../../../../App";
import { SetBonus, ButtonBar } from "../../../CommonCpns";
import { MyWpCard, MyArtCard } from "../../../Readers/MyItemCard";
import StatsTable from "../../../Readers/StatsTable";
import { colorCodes } from "../../../../data/misc";
import { deepCopy } from "../../../../helpers/misc";

const StyledDetails = styled.div`
  .details_card {
    max-height: ${(props) =>
      props.maxHeight ? props.maxHeight + "px" : "none"};
  }
  &.large .details_card {
    width: 20.25rem;
    padding: 1rem 0.75rem;
    border-left: 2px solid ${colorCodes.cardBg2};
    border-radius: 0 0.5rem 0.5rem 0;
    background-color: ${colorCodes.cardBg1};
  }
`;

export default function Details(props) {
  const { ownerIndex, opened, large } = props;
  const { pieces, sets } = props.art;

  useEffect(() => {
    if (
      (opened >= 0 && opened < 5 && !pieces[opened]) ||
      (opened === "set bonus" && !sets.length)
    ) {
      props.close();
    }
  }, [opened, ownerIndex]);

  let Cpn;
  if (opened === "weapon") {
    Cpn = <Weapon weapon={props.weapon} setWpInvOn={props.setWpInvOn} />;
  } else {
    if (opened === "set bonus") {
      Cpn = (
        <div className="details_card flex">
          <div className="px-1 hide-sb">
            <SetBonus sets={sets} />
          </div>
        </div>
      );
    } else if (opened === "stats bonus") {
      Cpn = <ArtAllStats artSBnes={props.artSBnes} />;
    } else {
      if (opened === -1 || !pieces[opened]) {
        return null;
      }
      Cpn = (
        <Artifact
          artP={pieces[opened]}
          ownerIndex={ownerIndex}
          setShowing={props.setShowing}
          opened={opened}
          setOpened={props.setOpened}
          openArtInv={props.openArtInv}
        />
      );
    }
  }
  return (
    <StyledDetails
      className={"py-2" + (large ? " large" : "")}
      maxHeight={large ? props.height - 16 : props.height - 68}
    >
      {Cpn}
    </StyledDetails>
  );
}

function Weapon(props) {
  return (
    <div className="details_card flex-col">
      <div className="px-1 grow-1 hide-sb">
        <MyWpCard weapon={props.weapon} mutable={true} />
      </div>
      <ButtonBar
        className="mt-3"
        texts={["Switch"]}
        variants={["positive"]}
        handlers={[() => props.setWpInvOn(true)]}
      />
    </div>
  );
}

function Artifact(props) {
  const { artP, ownerIndex } = props;
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  return (
    <div className="details_card">
      <div className="pb-2 hide-sb">
        <MyArtCard art={artP} mutable={true} />
      </div>
      <ButtonBar
        className="mt-4"
        texts={["Remove", "Switch"]}
        handlers={[
          () => {
            props.setShowing(false);
            setTimeout(() => {
              props.setOpened(-1);
              setMyChars((prev) => {
                const newChars = deepCopy(prev);
                newChars[ownerIndex].artIDs[props.opened] = null;
                return newChars;
              });
              setMyArts((prev) => {
                const newArts = deepCopy(prev);
                newArts[
                  newArts.findIndex((art) => art.ID === artP.ID)
                ].user = null;
                return newArts;
              });
            }, 200);
          },
          props.openArtInv
        ]}
      />
    </div>
  );
}

function ArtAllStats(props) {
  return (
    <div className="details_card flex">
      <div className="custom-sb">
        <StatsTable stats={props.artSBnes} />
      </div>
    </div>
  );
}
