import { memo } from "react";
import { Text, Img } from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { TalentXtraLv, InfoSign } from "../../CommonCpns";
import { wikia, unknownIcons } from "../../../data/misc";
import { getXtraTalentLv } from "../../../calculators/helpers";

export default memo(Overview);

function Overview(props) {
  const { char, charData } = props;
  return (
    <div className="full-h hide-sb">
      {charData.actvTalents.map((tl, i) => (
        <ActvTalent
          key={i}
          index={i}
          char={char}
          tlData={tl}
          party={props.party}
          vision={charData.vision}
          tlLevel={char[tl.type]}
          upgradeTl={(e) => props.upgradeTl(tl.type, +e.target.value)}
          toDetails={() => props.toDetails(i)}
        />
      ))}
      {charData.pasvTalents.map((tl, i) => {
        const n = tl.type.slice(-1);
        return (
          <PasvTalent
            key={i}
            vision={charData.vision}
            tlData={tl}
            available={isNaN(n) || char.ascension >= +n ? "" : " unavailable"}
            toDetails={() => props.toDetails(charData.actvTalents.length + i)}
          />
        );
      })}
    </div>
  );
}

function ActvTalent(props) {
  const { tlData, char, vision } = props;
  const xtraLv = getXtraTalentLv(tlData, char, props.party || []);
  return (
    <div className={(props.index ? "mt-3 " : "") + "flex"}>
      <Img
        className="my-2 mr-2"
        width="3.25rem"
        src={wikia + (tlData.image || unknownIcons[vision]) + ".png"}
        alt=""
        draggable={false}
      />
      <div className="grow-1 flex align-center">
        <div className="px-2">
          <Text fontWeight={700}>{tlData.name}</Text>
          <div className="flex align-center">
            <Text className="mr-1">Lv.</Text>
            {tlData.type === "Alternate Sprint" ? (
              <Text className="ml-1" color={vision} fontWeight={700}>
                1
              </Text>
            ) : (
              <Select
                color={vision}
                fontWeight={700}
                value={props.tlLevel}
                onChange={props.upgradeTl}
              >
                {[...Array(10).keys()].map((_, i) => (
                  <option key={i}>{i + 1}</option>
                ))}
              </Select>
            )}
            {Boolean(xtraLv) && <TalentXtraLv xtraLv={xtraLv} />}
          </div>
        </div>
        <div className="ml-auto" onClick={props.toDetails}>
          <InfoSign selfHover={true} />
        </div>
      </div>
    </div>
  );
}

function PasvTalent(props) {
  const { tlData, available, vision } = props;
  return (
    <div className="mt-3 flex">
      <Img
        className={"my-2 mr-2" + available}
        width="3.25rem"
        src={wikia + (tlData.image || unknownIcons[vision]) + ".png"}
        alt=""
        draggable={false}
      />
      <div className="grow-1 flex align-center">
        <div className={"px-2" + available}>
          <Text fontWeight={700}>{tlData.name}</Text>
          <div className="flex">
            <Text className="mr-2">Lv.</Text>
            <Text color={vision} fontWeight={700}>
              1
            </Text>
          </div>
        </div>
        <div className="ml-auto" onClick={props.toDetails}>
          <InfoSign selfHover={true} />
        </div>
      </div>
    </div>
  );
}
