import { Green, LGreen, Red } from "../../../../styledCpns/DataDisplay";
import Ayato from "./index";

export const buffs = [
  {
    index: 0,
    src: "Elemental Skill",
    desc: () => (
      <>
         Causes {Ayato.actvTalents[1].desc[1].lines[0]}
        <br /> Wave Flash increases {Ayato.actvTalents[1].desc[1].lines[1]}
      </>
    ),
    isGranted: () => true,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    index: 1,
    src: "Elemental Burst",
    desc: ({ inputs }) => (
      <>
        Increases the <LGreen>ATK SPD</LGreen> of characters (excluding Ayato
        himself) within its AoE by{" "}
        <Green>{Ayato.buffs[1].bnValue(inputs[0])}%</Green>.
      </>
    ),
    isGranted: () => true,
    affect: "teammates",
    labels: ["Elemental Burst Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [15],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Normal ATK SPD"] += Ayato.buffs[1].bnValue(inputs[0]);
    },
    bnValue: (lv) => (lv ? (lv <= 10 ? 7 + lv / 2 : 12) : 0)
  },
  {
    index: 2,
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => (
      <>
        {Ayato.pasvTalents[1].desc}{" "}
        <Red>DMG Bonus (base): {Ayato.buffs[2].bnValue(ATTRs)}.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addFinalBnes: ({ bonuses, ATTRs }) => {
      bonuses.EB.Flat += Ayato.buffs[2].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => Math.round((ATTRs.HP * 3) / 100)
  },
  {
    index: 3,
    src: "Constellation 1",
    desc: () => Ayato.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "self"
  },
  {
    index: 4,
    src: "Constellation 2",
    desc: () => Ayato.constellation[1].buff,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["HP%"] += 40;
    }
  },
  {
    index: 5,
    src: "Constellation 4",
    desc: () => Ayato.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "teammates",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 20;
    }
  }
];
