import { Box } from "../../../../styledCpns/Layout";
import { Text } from "../../../../styledCpns/DataDisplay";
import { InfoSign } from "../../../CommonCpns";
import Thumb from "../../../Readers/Thumb";
import weapons from "../../../../data/weapons";
import artifacts from "../../../../data/artifacts";
import { wikia, artifactIcons, artTypes } from "../../../../data/misc";

export default function Overview(props) {
  const { weapon, art, opened, toggleDetails } = props;
  return (
    <>
      <div className="flex-wrap">
        {Boolean(weapon) && (
          <Weapon
            weapon={weapon}
            chosen={opened === "weapon"}
            choose={() => toggleDetails("weapon")}
          />
        )}
        {art.pieces.map((artP, i) =>
          artP ? (
            <ArtPiece
              key={i}
              artP={artP}
              chosen={opened === i}
              choose={() => toggleDetails(i)}
            />
          ) : (
            <ArtIcon key={i} index={i} openArtInv={() => props.setArtInvI(i)} />
          )
        )}
      </div>
      <SetBonus
        sets={art.sets}
        chosen={opened === "set bonus"}
        toggleDetails={() => toggleDetails("set bonus")}
      />
      <StatsBonus
        chosen={opened === "stats bonus"}
        toggleDetails={() => toggleDetails("stats bonus")}
      />
    </>
  );
}

function Weapon({ weapon: { type, name, ...others }, chosen, choose }) {
  const { icon, rarity } = weapons[type][name];
  return (
    <Box className="p-1" width="33.33%">
      <Thumb
        noDeco
        item={{ icon, rarity, ...others }}
        chosen={window.innerWidth < 686 ? false : chosen}
        choose={choose}
      />
    </Box>
  );
}

function ArtPiece({ artP: { set, type, level }, chosen, choose }) {
  const { icon, rarity = 5 } = artifacts[set][type];
  return (
    <Box className="p-1" width="33.33%">
      <Thumb
        noDeco
        item={{ rarity, level, icon }}
        chosen={window.innerWidth < 686 ? false : chosen}
        choose={choose}
      />
    </Box>
  );
}

function ArtIcon(props) {
  return (
    <Box className="p-1" width="33%" height="7.75rem">
      <Box
        className="p-3 full-h flex-center pointer glow-on-hover"
        borderRadius="0.25rem"
        bgColor="cardBg3"
        onClick={props.openArtInv}
      >
        <img
          className="full-w"
          src={wikia + artifactIcons[artTypes[props.index]] + ".png"}
          alt=""
        />
      </Box>
    </Box>
  );
}

function Group(props) {
  return (
    <Box
      className={
        `mt-${props.mt} p-2 flex justify-between align-center contain-info_sign` +
        (props.chosen ? " chosen" : "")
      }
      borderRadius="0.5rem"
      bgColor={props.chosen ? "cardBg2" : "transparent"}
      onClick={props.toggle}
    >
      {props.content}
      {props.available && <InfoSign on={props.chosen} />}
    </Box>
  );
}

function SetBonus(props) {
  const { sets } = props;
  return (
    <Group
      mt={3}
      chosen={props.chosen}
      content={
        <div>
          <Text variant="h6" color="orange" fontWeight={700}>
            Set Bonus
          </Text>
          <div className="mt-1 pl-2">
            {sets.length ? (
              <>
                <Text color="green" fontWeight={700}>
                  {sets[0].name} ({sets[0].bonusLv * 2 + 2})
                </Text>
                {Boolean(sets[1]) && (
                  <Text className="mt-1" color="green" fontWeight={700}>
                    {sets[1].name} (2)
                  </Text>
                )}
              </>
            ) : (
              <Text color="rgb(185, 185, 185)" fontWeight={700}>
                No Set Bonus
              </Text>
            )}
          </div>
        </div>
      }
      toggle={() => {
        if (sets.length) props.toggleDetails();
      }}
      available={!!sets.length}
    />
  );
}

function StatsBonus(props) {
  return (
    <Group
      mt={2}
      chosen={props.chosen}
      content={
        <Text variant="h6" color="orange" fontWeight={700}>
          Artifact Details
        </Text>
      }
      toggle={props.toggleDetails}
      available={true}
    />
  );
}
