import characters from "../data/characters";
import { elmtDmgBnes } from "../data/misc";

export function getEBcost(name) {
  if (name) {
    for (let tl of characters[name].actvTalents) {
      if (tl.type === "Elemental Burst") return tl.energyCost;
    }
  }
  return 0;
}

export function incAllNAs(bonuses, val) {
  for (let type of ["NA", "CA", "PA"]) {
    bonuses[type].DMG += val;
  }
}

export function incAllElmtDmgBnes(ATTRs, val) {
  for (let type of elmtDmgBnes) {
    ATTRs[type] += val;
  }
}

export function incElementalBnes(name, infusion, elmt, bonuses, bnType, value) {
  if (characters[name].vision === elmt) {
    bonuses.Elemental[bnType] += value;
  }
  for (let type of ["NA", "CA", "PA"]) {
    if (infusion[type] === elmt) {
      bonuses[type][bnType] += value;
    }
  }
}

export function checkCharMC(mods, char, charBCs, index) {
  const ctrl = charBCs.find((c) => c.index === index);
  return (
    mods.find((m) => m.index === index).isGranted(char) &&
    ctrl &&
    ctrl.activated
  );
}

export function findInput(MCs, ctrlI, inpI, defaultVal = 0) {
  const ctrl = MCs.find(({ index }) => index === ctrlI);
  return ctrl && ctrl.inputs ? ctrl.inputs[inpI] : defaultVal;
}
