import {
  initCharMCs,
  getMainWpBCs,
  initSubWpMCs,
  getSubArtBCs,
  getSubArtDCs,
  initParty,
  initElmtMCs
} from "../../helpers";
import { initWeapon } from "../../../Picker/ItemPicker";
import characters from "../../../../data/characters";

export function applySettings(tempSetups, charName, CBs) {
  function getResult(indexes, prev, newElmt) {
    const result = [];
    for (let index of indexes) {
      if (index === null) result.push(newElmt);
      else result.push(prev[index]);
    }
    return result;
  }
  const setups = [];
  const cmpSetupIs = [];
  const indexes = [];
  tempSetups.forEach(({ name, ID, type, checked, index }, i) => {
    setups.push({ name, ID, type });
    if (checked) {
      cmpSetupIs.push(i);
    }
    indexes.push(index);
  });
  CBs.setSetups(setups);
  CBs.setCmpSetupIs(cmpSetupIs);
  CBs.setSelfMCs((prev) =>
    getResult(indexes, prev, initCharMCs(charName, true))
  );
  CBs.setWpRack((prev) => {
    const result = [];
    let ID = Date.now();
    for (let index of indexes) {
      if (index === null) {
        const { weapon } = characters[charName];
        const newWp = initWeapon({ ID: ID++, type: weapon });
        result.push({ ...newWp, BCs: getMainWpBCs(weapon, newWp.name) });
      } else {
        result.push(prev[index]);
      }
    }
    return result;
  });
  CBs.setSubWpMCs((prev) => getResult(indexes, prev, initSubWpMCs()));
  CBs.setArtChest((prev) =>
    getResult(indexes, prev, {
      pieces: [null, null, null, null, null],
      sets: [],
      BCs: [],
      subBCs: getSubArtBCs(),
      subDCs: getSubArtDCs()
    })
  );
  CBs.setParties((prev) => getResult(indexes, prev, initParty()));
  CBs.setElmtMCs((prev) => getResult(indexes, prev, initElmtMCs()));
}
