import { memo } from "react";
import isEqual from "react-fast-compare";
import { Box, AbsoluteBox } from "../../styledCpns/Layout";
import { Img, Text } from "../../styledCpns/DataDisplay";
import { BetaMark } from "../CommonCpns";
import { Cell } from "./Body.styles";
import { wikia, visionIcons } from "../../data/misc";

export default function Body({ forChar, data, each, pick }) {
  return (
    <div className="pr-3 full-h overflow-auto custom-sb">
      <div className="flex-wrap">
        {data.map((item, i) => (
          <MemoItem
            key={item.code.toString() + item.rarity}
            forChar={forChar}
            pickedAmount={each?.[i]}
            item={item}
            index={i}
            pick={pick}
          />
        ))}
      </div>
    </div>
  );
}

const MemoItem = memo(Item, isEqual);

function Item({ forChar, pickedAmount, item, index, pick }) {
  if (item.existed) return <></>;
  const hasCons = item.constellation !== undefined;
  return (
    <Cell className={forChar ? "w-vision" : "wo-vision"}>
      <Thumb
        item={item}
        forChar={forChar}
        pickedAmount={pickedAmount}
        pick={() => pick(item, index)}
      />
      {forChar && (
        <div className={"badge" + (hasCons ? " w-cons" : "")}>
          {hasCons && <p className="cons">C{item.constellation}</p>}
          <Img
            width="20px"
            src={wikia + visionIcons[item.vision] + ".png"}
            alt="vision"
            draggable={false}
          />
        </div>
      )}
    </Cell>
  );
}

function Thumb({ item, forChar, pickedAmount, pick }) {
  return (
    <div className="pointer zoomin-on-hover relative" onClick={pick}>
      {item.beta && (
        <AbsoluteBox top={0} left={0} zIndex={1}>
          <BetaMark />
        </AbsoluteBox>
      )}
      <div className="relative">
        <Box
          className={"overflow-hidden flex " + (forChar ? "pt-3" : "p-1")}
          borderRadius="0.5rem 0.5rem 0 0"
          bgImg={item.rarity}
        >
          <Img
            src={item.beta && !forChar ? item.icon : wikia + item.icon + ".png"}
            alt=""
            width="100%"
            height="100%"
            draggable={false}
          />
        </Box>
        {Boolean(pickedAmount) && (
          <AbsoluteBox bottom={0} right="0.25rem">
            <Text color="black" fontWeight={700}>
              {pickedAmount}
            </Text>
          </AbsoluteBox>
        )}
      </div>
      <Box borderRadius="0 0 0.5rem 0.5rem" bgColor="defaultBg">
        <Text
          className="px-2 ellipsis"
          color="black"
          fontWeight={700}
          align="center"
        >
          {item.name}
        </Text>
      </Box>
    </div>
  );
}
