const Shenhe = {
  code: 47,
  icon: "5/58/Character_Shenhe_Thumb",
  sideIcon: "8/8d/Character_Shenhe_Side_Icon",
  rarity: 5,
  nation: "Liyue",
  vision: "Cryo",
  weapon: "Polearm",
  stats: mapToLevels([
    {
      "Base HP": 1011,
      "Base ATK": 24,
      "Base DEF": 65
    },
    {
      "Base HP": 2624,
      "Base ATK": 61,
      "Base DEF": 168
    },
    {
      "Base HP": 3491,
      "Base ATK": 82,
      "Base DEF": 223
    },
    {
      "Base HP": 5224,
      "Base ATK": 122,
      "Base DEF": 334
    },
    {
      "Base HP": 5840,
      "Base ATK": 137,
      "Base DEF": 373,
      "ATK%": 7.2
    },
    {
      "Base HP": 6719,
      "Base ATK": 157,
      "Base DEF": 429,
      "ATK%": 7.2
    },
    {
      "Base HP": 7540,
      "Base ATK": 176,
      "Base DEF": 482,
      "ATK%": 14.4
    },
    {
      "Base HP": 8429,
      "Base ATK": 197,
      "Base DEF": 538,
      "ATK%": 14.4
    },
    {
      "Base HP": 9045,
      "Base ATK": 211,
      "Base DEF": 578,
      "ATK%": 14.4
    },
    {
      "Base HP": 9941,
      "Base ATK": 232,
      "Base DEF": 635,
      "ATK%": 14.4
    },
    {
      "Base HP": 10557,
      "Base ATK": 247,
      "Base DEF": 674,
      "ATK%": 21.6
    },
    {
      "Base HP": 11463,
      "Base ATK": 268,
      "Base DEF": 732,
      "ATK%": 21.6
    },
    {
      "Base HP": 12080,
      "Base ATK": 282,
      "Base DEF": 772,
      "ATK%": 28.8
    },
    {
      "Base HP": 12993,
      "Base ATK": 304,
      "Base DEF": 830,
      "ATK%": 28.8
    }
  ]),
  actvTalents: [
    {
      type: "Normal Attack",
      name: "Dawnstar Shooter",
      image: "1/16/Talent_Dawnstar_Piercer",
      desc: PolearmDesc(5),
      stats: [
        {
          name: "1-Hit",
          dmgTypes: ["NA", "Physical"],
          baseMult: 43.26,
          multType: 1
        },
        {
          name: "2-Hit",
          dmgTypes: ["NA", "Physical"],
          baseMult: 40.25,
          multType: 1
        },
        {
          name: "3-Hit",
          dmgTypes: ["NA", "Physical"],
          baseMult: 53.32,
          multType: 1
        },
        {
          name: "4-Hit (x2)",
          dmgTypes: ["NA", "Physical"],
          baseMult: 26.32,
          multType: 1
        },
        {
          name: "5-Hit",
          dmgTypes: ["NA", "Physical"],
          baseMult: 65.62,
          multType: 1
        },
        {
          name: "Charged Attack",
          dmgTypes: ["CA", "Physical"],
          baseMult: 110.67,
          multType: 1
        },
        CaStamina(25),
        ...mediumPAs
      ]
    },
    {
      type: "Elemental Skill",
      name: "Spring Spirit Summoning",
      image: "6/6c/Talent_Spring_Spirit_Summoning",
      desc: [
        {
          content: (
            <>
              Grants all nearby party members the Icy Quill effect and deals{" "}
              <Cryo>Cryo DMG</Cryo> in different ways based on whether it is
              Tapped/Pressed or held.
            </>
          )
        },
        {
          heading: "Press",
          content: (
            <>
              Rushes forward together with a Talisman Spirit, dealing{" "}
              <Cryo>Cryo DMG</Cryo> to opponents along the path.
            </>
          )
        },
        {
          heading: "Hold",
          content: (
            <>
              Commands the Talisman Spirit to deal AoE <Cryo>Cryo DMG</Cryo>.
            </>
          )
        },
        {
          heading: "Icy Quill",
          content: (
            <>
              When Normal, Charged and Plunging Attacks, Elemental Skills, and
              Elemental Bursts deal <Cryo>Cryo DMG</Cryo> the{" "}
              <LGreen>DMG</LGreen> dealt is increased based on Shenhe's{" "}
              <LGreen>current ATK</LGreen>.
            </>
          )
        },
        {
          content: (
            <>
              The Icy Quill's effects will be cleared once its duration ends or
              after being triggered a certain number of times. When held rather
              than Tapped/Pressed, the Icy Quill's effect lasts longer and can
              be triggered more times.
              <br />
              When one <Cryo>Cryo DMG</Cryo> instance strikes multiple
              opponents, the effect is triggered multiple times based on the
              number of opponents hit. The number of times the effect is
              triggered is calculated independently for each party member with
              the Icy Quill.
            </>
          )
        }
      ],
      getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
      stats: [
        {
          name: "Press Skill DMG",
          dmgTypes: ["ES", "Elemental"],
          baseMult: 139.2,
          multType: 2
        },
        {
          name: "Hold Skill DMG",
          dmgTypes: ["ES", "Elemental"],
          baseMult: 188.8,
          multType: 2
        },
        {
          name: "DMG Bonus",
          baseMult: 45.66,
          multType: 2
        }
      ],
      otherStats: () => [
        {
          name: "Press/Hold Duration",
          value: "10s/15s"
        },
        {
          name: "Press/Hold Trigger Quota",
          value: "5/7"
        },
        {
          name: "Press CD",
          value: "10s"
        },
        {
          name: "Hold CD",
          value: "15s"
        }
      ]
    },
    {
      type: "Elemental Burst",
      name: "Divine Maiden's Deliverance",
      image: "d/d5/Talent_Divine_Maiden%27s_Deliverance",
      desc: [
        {
          get content() {
            return (
              <>
                Unleashes the power of the Talisman Spirit, allowing it to roam
                free in this plane, dealing AoE <Cryo>Cryo DMG</Cryo>.
                <br />
                {this.debuff}. It also deals periodic <Cryo>Cryo DMG</Cryo> to
                opponents within the field.
              </>
            );
          },
          debuff: (
            <>
              The field decreases the <LGreen>Cryo RES</LGreen> and{" "}
              <LGreen>Physical RES</LGreen> of opponents within it
            </>
          )
        }
      ],
      getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
      stats: [
        {
          name: "Skill DMG",
          dmgTypes: ["EB", "Elemental"],
          baseMult: 100.8,
          multType: 2
        },
        {
          name: "RES Decrease",
          noCalc: true,
          getValue: (lv) => Math.min(5 + lv, 15) + "%"
        },
        {
          name: "DoT",
          dmgTypes: ["EB", "Elemental"],
          baseMult: 33.12,
          multType: 2
        }
      ],
      otherStats: (lv) => [
        {
          name: "Duration",
          value: "12s"
        },
        {
          name: "CD",
          value: "20s"
        }
      ],
      energyCost: 80
    }
  ],
  pasvTalents: [
    {
      type: "Ascension 1",
      name: "Deific Embrace",
      image: "2/29/Talent_Deific_Embrace",
      desc: (
        <>
          An active character within the field created by Divine Maiden's
          Deliverance gain <Green>15%</Green> <LGreen>Cryo DMG Bonus</LGreen>.
        </>
      )
    },
    {
      type: "Ascension 4",
      name: "Spirit Communion Seal",
      image: "5/5c/Talent_Spirit_Communion_Seal",
      get desc() {
        return (
          <>
            {this.lines[0]}
            {this.lines[1]}
            {this.lines[2]}
          </>
        );
      },
      lines: [
        <>
          After Shenhe uses Spring Spirit Summoning, she will grant all nearby
          party members the following effects:
        </>,
        <>
          <br /> Press:{" "}
          <LGreen>Elemental Skill and Elemental Burst DMG</LGreen> increased by{" "}
          <Green>15%</Green> for 10s.
        </>,
        <>
          <br /> Hold: <LGreen>Normal, Charged and Plunging Attack DMG</LGreen>{" "}
          increased by <Green>15%</Green> for 15s.
        </>
      ]
    },
    {
      type: "Passive",
      name: "Precise Comings and Goings",
      image: "d/d0/Talent_Precise_Comings_and_Goings",
      desc: (
        <>
          Gains <Green>25%</Green> more <LGreen>rewards</LGreen> when dispatched
          on a Liyue <LGreen>Expedition</LGreen> for 20 hours.
        </>
      )
    }
  ],
  constellation: [
    {
      name: "Clarity of Heart",
      image: "1/13/Constellation_Clarity_of_Heart",
      desc: (
        <>
          Spring Spirit Summoning can be used <Green>1</Green>{" "}
          <LGreen>more time</LGreen>.
        </>
      )
    },
    {
      name: "Centered Spirit",
      image: "9/90/Constellation_Centered_Spirit",
      desc: (
        <>
          Divine Maiden's Deliverance lasts for <Green>6s</Green>{" "}
          <LGreen>longer</LGreen>. Active characters within Divine Maiden's
          Deliverance's field deal <Green>15%</Green> increased{" "}
          <Cryo>Cryo</Cryo> <LGreen>CRIT DMG</LGreen>.
        </>
      )
    },
    {
      name: "Seclusion",
      image: "d/d9/Constellation_Seclusion",
      desc: "Spring Spirit Summoning"
    },
    {
      name: "Insight",
      image: "4/46/Constellation_Insight",
      desc: (
        <>
          Every time a character triggers Icy Quill's DMG Bonus, Shenhe will
          gain a Skyfrost Mantra stack. Each stack increases her next{" "}
          <LGreen>Spring Spirit Summoning's DMG</LGreen> by <Green>5%</Green>.
          Stacks last for 60s and has a <LGreen>maximum</LGreen> of{" "}
          <Green>50</Green>.
        </>
      )
    },
    {
      name: "Divine Attainment",
      image: "5/58/Constellation_Divine_Attainment",
      desc: "Divine Maiden's Deliverance"
    },
    {
      name: "Mystical Abandon",
      image: "0/0d/Constellation_Mystical_Abandon",
      desc: (
        <>
          When characters trigger Icy Quill's effects using{" "}
          <LGreen>Normal and Charged Attack DMG</LGreen>, it does not count
          toward the Trigger Quota.
        </>
      )
    }
  ],
  buffs: [
    {
      index: 0,
      src: "Elemental Skill",
      desc: ({ toSelf, inputs }) => (
        <>
          {Shenhe.actvTalents[1].desc[3].content}{" "}
          {!toSelf && (
            <Red>DMG Bonus (base): {Shenhe.buffs[0].bnValue(...inputs)}.</Red>
          )}
        </>
      ),
      isGranted: () => true,
      affect: "party",
      labels: ["Current ATK", "Elemental Skill Level"],
      inputs: [0, 1],
      inputTypes: ["text", "text"],
      maxs: [9999, 13],
      addFinalBnes: ({
        toSelf,
        char,
        ATTRs,
        bonuses,
        inputs,
        infusion,
        party
      }) => {
        const { bnValue } = Shenhe.buffs[0];
        const value = toSelf
          ? bnValue(
              ATTRs.ATK,
              char["Elemental Skill"] +
                getXtraTalentLv(Shenhe.actvTalents[1], char, party)
            )
          : bnValue(...inputs);
        incElementalBnes(char.name, infusion, "Cryo", bonuses, "Flat", value);
      },
      bnValue: (ATK, level) =>
        Math.round((ATK * 45.66 * TalentLvMults[2][level]) / 100)
    },
    {
      index: 1,
      src: "Ascension 1 Passive Talent",
      desc: () => Shenhe.pasvTalents[0].desc,
      isGranted: (char) => char.ascension >= 1,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["Cryo DMG Bonus"] += 15;
      }
    },
    {
      index: 2,
      src: "Ascension 4 Passive Talent",
      desc: ({ inputs }) => (
        <>
          {Shenhe.pasvTalents[1].lines[0]}
          <span className={inputs[0] ? "" : "unavailable"}>
            {Shenhe.pasvTalents[1].lines[1]}
          </span>
          <span className={inputs[1] ? "" : "unavailable"}>
            {Shenhe.pasvTalents[1].lines[2]}
          </span>
        </>
      ),
      isGranted: (char) => char.ascension >= 4,
      affect: "party",
      selfLabels: ["Press", "Hold"],
      labels: ["Press", "Hold"],
      inputs: [true, false],
      inputTypes: ["check", "check"],
      addBnes: ({ bonuses, inputs }) => {
        if (inputs[0]) {
          bonuses.ES.DMG += 15;
          bonuses.EB.DMG += 15;
        }
        if (inputs[1]) {
          bonuses.NA.DMG += 15;
          bonuses.CA.DMG += 15;
          bonuses.PA.DMG += 15;
        }
      }
    },
    {
      index: 3,
      src: "Constellation 2",
      desc: () => Shenhe.constellation[1].desc,
      isGranted: (char) => char.constellation >= 2,
      affect: "party",
      addBnes: ({ char, bonuses, infusion }) => {
        incElementalBnes(char.name, infusion, "Cryo", bonuses, "CD", 0.15);
      }
    },
    {
      index: 4,
      src: "Constellation 4",
      desc: () => Shenhe.constellation[3].desc,
      isGranted: (char) => char.constellation >= 4,
      affect: "self",
      selfLabels: ["Stacks"],
      inputs: [0],
      inputTypes: ["text"],
      maxs: [50],
      addBnes: ({ bonuses, inputs }) => {
        bonuses.ES.DMG += 5 * inputs[0];
      }
    }
  ],
  debuffs: [
    {
      index: 0,
      src: "Elemental Burst",
      desc: ({ fromSelf, char, inputs, party }) => (
        <>
          {Shenhe.actvTalents[2].desc[0].debuff} by{" "}
          <Green>
            {Shenhe.debuffs[0].pntValue(fromSelf, char, inputs, party)}%
          </Green>
          .
        </>
      ),
      isGranted: () => true,
      labels: ["Elemental Burst Level"],
      inputTypes: ["text"],
      addPntes: ({ rdMult, fromSelf, char, inputs, party }) => {
        const value = Shenhe.debuffs[0].pntValue(fromSelf, char, inputs, party);
        rdMult["Physical RES"] -= value;
        rdMult["Cryo RES"] -= value;
      },
      pntValue: (fromSelf, char, inputs, party) => {
        let value = 0;
        if (fromSelf) {
          value =
            5 +
            char["Elemental Burst"] +
            getXtraTalentLv(Shenhe.actvTalents[2], char, party);
        } else if (inputs[0]) {
          value = 5 + inputs[0];
        }
        return Math.min(value, 15);
      }
    }
  ]
};

export default Shenhe;
