import styled from "styled-components";
import { useState } from "react";
import { ModalCarpet, Box } from "../styledCpns/Layout";
import { useCloseModal } from "../helpers/hooks";
import { SeeDetails, ButtonBar } from "./CommonCpns";
import { Img, Text, ColoredSpan } from "../styledCpns/DataDisplay";
import characters from "../data/characters";
import { wikia } from "../data/misc";

const Details = styled.div`
  height: 0;
  transition: height 200ms linear;
  &.on {
    margin-top: 0.5rem;
    height: 60vh;
  }
`;

export default function OutdatesDisplayer(props) {
  const [on, setOn] = useState(false);
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 white-glow-shadow max-width-95"
        width="30rem"
        borderRadius="0.5rem"
        bgColor="cardBg2"
      >
        <Text color="green" variant="h6" align="center">
          Successfully loaded your data
        </Text>
        <Text className="mt-1" color="lightRed">
          We have removed outdated buffs / debuffs in your Setup(s).
        </Text>
        <SeeDetails
          className={"mt-1" + (on ? " active" : "")}
          onClick={() => setOn(!on)}
        />
        <Details className={"hide-sb" + (on ? " on" : "")}>
          {props.outdates.map((od, i) => (
            <Outdate key={i} i={i} od={od} />
          ))}
        </Details>
        <ButtonBar
          className="mt-3"
          texts={["Confirm"]}
          variants={["positive"]}
          handlers={[() => props.close()]}
        />
      </Box>
    </ModalCarpet>
  );
}

function Outdate({ i, od }) {
  return (
    <Box
      className={(i ? "mt-2 " : "") + "p-3"}
      borderRadius="0.5rem"
      bgColor="cardBg3"
    >
      <div className="flex justify-between align-start">
        <Text variant="h6" color="orange" fontWeight={700}>
          {od.name}
        </Text>
        <Members members={od.members} />
      </div>
      {od.list.map((item, j) => (
        <Item key={j} {...item} />
      ))}
    </Box>
  );
}

function Members({ members }) {
  return (
    <div className="ml-2 flex">
      {members.map((name, k) => (
        <Box
          key={k}
          className={"flex align-center" + (k ? " ml-1" : "")}
          height="2rem"
        >
          <Img
            height="100%"
            src={wikia + characters[name].icon + ".png"}
            alt={name}
          />
          {!k && members.length > 1 && (
            <Text className="ml-1" variant="h4">
              +
            </Text>
          )}
        </Box>
      ))}
    </div>
  );
}

function Item({ name, type, index }) {
  const { src, desc } = characters[name][type].find((m) => m.index === index);
  return (
    <Box className="mt-2 px-3 p-2" borderRadius="0.25rem" bgColor="cardBg1">
      <Text>
        Character:{" "}
        <ColoredSpan color={characters[name].vision}>
          <b>{name}</b>
        </ColoredSpan>
      </Text>
      <Text>
        Source: <ColoredSpan color="lightGold">{src}</ColoredSpan> (
        {type.slice(0, -1)})
      </Text>
      <Text className="mt-2">{desc()}</Text>
    </Box>
  );
}
