import characters from "../data/characters";
import weapons from "../data/weapons";
import artifacts from "../data/artifacts";
import { allStatsTypes } from "../data/artifacts/config";
import { elmts, ampRxnTrigElmts, rsnNames } from "../data/elements";
import { levels } from "../data/misc";

export const specialCodes = {
  Pyro: "PR",
  Hydro: "HD",
  Dendro: "DD",
  Electro: "ELT",
  Anemo: "ANM",
  Cryo: "CO",
  Geo: "GO",
  Recitative: "RCTT",
  Aria: "ARA",
  Interlude: "ITL"
};

export default function encodeExportedData(data) {
  const { name, level, ascension, constellation, ...talents } = data.char;
  return [
    [
      characters[name].code,
      levels.indexOf(level),
      constellation,
      ...["Normal Attack", "Elemental Skill", "Elemental Burst"].map(
        (type) => talents[type]
      )
    ].join("&"),
    [getMCsCode(data.selfMCs.BCs), getMCsCode(data.selfMCs.DCs)].join("&"),
    getWpCode(data.weapon),
    getSubWpBCsCode(data.subWpMCs.BCs),
    // subWpDCs can be joined with subWpBCs later by "&"
    getArtCode(data.art),
    getPartyCode(data.party),
    getElmtMCsCode(data.elmtMCs),
    getTgCode(data.target)
  ].join("?");
}

function getWpCode(weapon) {
  const { type, name, level, refinement, BCs } = weapon;
  return [
    weapons[type][name].code,
    levels.indexOf(level),
    refinement,
    getMCsCode(BCs)
  ].join("&");
}

function getSubWpBCsCode(BCs) {
  const result = [];
  for (const type in BCs) {
    for (const ctrl of BCs[type]) {
      result.push(
        weapons[type][ctrl.name].code +
          ":" +
          ctrl.refinement +
          getInputsCode(ctrl.inputs)
      );
    }
  }
  return result.join("!");
}

function getArtCode(art) {
  const { pieces, BCs } = art;
  const result = [];

  const piecesCodes = [];
  for (const P of pieces) {
    if (P === null) {
      piecesCodes.push("X");
    } else {
      const infoCodes = [
        artifacts[P.set].code,
        P.rarity || 5,
        P.level,
        allStatsTypes.indexOf(P.mainSType)
      ];
      const subSCodes = [];
      for (const ss of P.subS) {
        subSCodes.push(allStatsTypes.indexOf(ss.type) + ":" + ss.val);
      }
      infoCodes.push(subSCodes.join("/"));
      piecesCodes.push(infoCodes.join("#"));
    }
  }
  result.push(piecesCodes.join("!"));
  result.push(getMCsCode(BCs));

  for (const mcs of ["subBCs", "subDCs"]) {
    const code = [];
    for (const ctrl of art[mcs]) {
      code.push(artifacts[ctrl.set].code + getInputsCode(ctrl.inputs));
    }
    result.push(code.join("!"));
  }

  return result.join("&");
}

function getPartyCode(party) {
  const result = [];
  for (const tm of party) {
    if (tm) {
      result.push(
        characters[tm.name].code +
          "#" +
          getMCsCode(tm.BCs) +
          "#" +
          getMCsCode(tm.DCs)
      );
    }
  }
  return result.join("&");
}

function getElmtMCsCode(elmtMCs) {
  const { naAmpRxn, ampRxn } = elmtMCs;
  return [
    !naAmpRxn ? "" : Object.keys(ampRxnTrigElmts).indexOf(naAmpRxn),
    !ampRxn ? "" : Object.keys(ampRxnTrigElmts).indexOf(ampRxn),
    elmtMCs.superconduct ? "T" : "F",
    elmtMCs.resonance
      .map(({ name }) => Object.values(rsnNames).indexOf(name))
      .join("#")
  ].join("&");
}

function getTgCode({ Level, ...Res }) {
  return [Level]
    .concat(["Physical", ...elmts].map((type) => Res[type + " RES"]))
    .join("&");
}

function getMCsCode(MCs) {
  return MCs.map((ctrl) => ctrl.index + getInputsCode(ctrl.inputs)).join("!");
}

function getInputsCode(inputs) {
  if (inputs) {
    const result = inputs
      .map((inp) => {
        if (inp === true) {
          return "T";
        } else if (inp === false) {
          return "F";
        } else if (specialCodes[inp]) {
          return specialCodes[inp];
        }
        return inp;
      })
      .join("/");
    return ":" + result;
  }
  return "";
}
