import { useCallback, useContext, useState } from "react";
import { MyArtsCt, MyCharsCt, SetDBCt } from "../../App";
import artifacts from "../../data/artifacts";
import characters from "../../data/characters";
import { artTypes } from "../../data/misc";
import { deepCopy } from "../../helpers/misc";
import { Card } from "../../styledCpns/Database";
import { Box } from "../../styledCpns/Layout";
import { ButtonBar, Confirmation, EquippedChar } from "../CommonCpns";
import Picker from "../Picker";
import { MyArtCard } from "../Readers/MyItemCard";

export default function RightSection({ art, setChosenID }) {
  const myChars = useContext(MyCharsCt);
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  const [pickerOn, setPickerOn] = useState(false);
  const [removing, setRemoving] = useState(false);
  const [swapTarget, setSwapTarget] = useState(null);

  const swap = useCallback(
    (name) => {
      if (!art) return;
      const { ID, user } = art;
      const i = artTypes.indexOf(art.type);
      const targetCharI = myChars.findIndex((char) => char.name === name);
      setMyArts((prev) => {
        const arts = deepCopy(prev);
        const artI_new = arts.findIndex((item) => item.ID === ID);
        arts[artI_new].user = name;
        const artID = myChars[targetCharI].artIDs[i];
        if (artID) {
          const artI_old = arts.findIndex((item) => item.ID === artID);
          arts[artI_old].user = user;
        }
        return arts;
      });
      setMyChars((prev) => {
        const newChars = deepCopy(prev);
        if (user) {
          const currentCharI = newChars.findIndex((char) => char.name === user);
          newChars[currentCharI].artIDs[i] = newChars[targetCharI].artIDs[i];
        }
        newChars[targetCharI].artIDs[i] = ID;
        return newChars;
      });
    },
    [art, myChars, setMyArts, setMyChars]
  );

  return (
    <div className="flex-col justify-between">
      <Card>
        <Box className="hide-sb" width="18.75rem" height="25rem">
          {art ? <MyArtCard art={art} mutable={true} /> : null}
        </Box>
        {art ? (
          <ButtonBar
            className="mt-3"
            texts={["Remove", "Equip"]}
            handlers={[() => setRemoving(true), () => setPickerOn(true)]}
          />
        ) : null}
      </Card>
      {art?.user ? <EquippedChar user={art.user} /> : null}
      {pickerOn && (
        <CharPicker
          user={art.user}
          myChars={myChars}
          setSwapTarget={setSwapTarget}
          swap={swap}
          close={() => setPickerOn(false)}
        />
      )}
      {removing && (
        <ConfirmRemove
          art={art}
          setChosenID={setChosenID}
          close={() => setRemoving(false)}
        />
      )}
      {swapTarget ? (
        <Confirmation
          message={
            <>
              <b>{art.user}</b> is currently using "
              <b>{artifacts[art.set][art.type].name}</b>". Swap?
            </>
          }
          close={() => setSwapTarget(null)}
          confirm={() => swap(swapTarget)}
        />
      ) : null}
    </div>
  );
}

function CharPicker({ user, setSwapTarget, swap, close }) {
  const myChars = useContext(MyCharsCt);
  const data = [];
  for (let { name, constellation } of myChars) {
    if (name !== user) {
      const { code, icon, rarity, vision, weapon } = characters[name];
      data.push({ code, name, icon, rarity, vision, weapon, constellation });
    }
  }
  return (
    <Picker
      data={data}
      dataType="Character"
      close={close}
      pick={({ name }) => {
        if (user) {
          setSwapTarget(name);
        } else {
          swap(name);
        }
      }}
    />
  );
}

function ConfirmRemove({ art: { ID, set, type, user }, setChosenID, close }) {
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  const myArts = useContext(MyArtsCt);
  const index = myArts.findIndex((item) => item.ID === ID);
  return (
    <Confirmation
      message={
        <>
          Remove "<b>{artifacts[set][type].name}</b>
          "?{" "}
          {Boolean(user) && (
            <>
              It is currently used by <b>{user}</b>.
            </>
          )}
        </>
      }
      close={close}
      confirm={() => {
        setMyArts((prev) => {
          const arts = deepCopy(prev);
          arts.splice(index, 1);
          return arts;
        });
        if (myArts.length > 1) {
          const newIndex = index + (index === myArts.length - 1 ? -1 : 1);
          setChosenID(myArts[newIndex].ID);
        }
        if (user) {
          setMyChars((prev) => {
            const newChars = deepCopy(prev);
            newChars[newChars.findIndex((unit) => unit.name === user)].artIDs[
              type
            ] = null;
            return newChars;
          });
        }
      }}
    />
  );
}
