import { useCallback, useState } from "react";
import weapons from "../../data/weapons";
import { StyledRack } from "../../styledCpns/Database";
import { NoItems } from "../CommonCpns";
import Thumb from "../Readers/Thumb";

export default function LeftSection(props) {
  return (
    <StyledRack className="hide-sb">
      {props.wps.length ? <Items {...props} /> : <NoItems type="Weapons" />}
    </StyledRack>
  );
}

function Items({ wps, chosenID, setChosenID }) {
  const [clickedI, setClickedI] = useState(null);
  const choose = useCallback(
    (id) => {
      setClickedI(null);
      setChosenID(id);
    },
    [setChosenID]
  );
  return (
    <div className="flex-wrap">
      {wps.map(({ type, name, ID, ...others }, i) => {
        const { beta, icon, rarity } = weapons[type][name];
        return (
          <div key={ID} className="cell relative">
            <Thumb
              index={i}
              item={{ beta, icon, rarity, ID, ...others }}
              chosen={ID === chosenID}
              clicked={i === clickedI}
              handleMouseDown={setClickedI}
              choose={choose}
            />
          </div>
        );
      })}
    </div>
  );
}
