import { useState, memo } from "react";
import isEqual from "react-fast-compare";
import { Text } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { IconBtn, Button } from "../../../styledCpns/Inputs";
import { Card } from "../../../styledCpns/Database";
import { Divider, LinkIcon } from "./styles";
import Members from "./Members";
import Gears from "./Gears";
import characters from "../../../data/characters";
import { restoreAllMCs } from "../../../helpers/processors";

function reCreateSetup(setup) {
  const { ID, name, type, char, target } = setup;
  return { ID, name, type, char, ...restoreAllMCs(setup), target };
}

export default memo(Setup, isEqual);

function Setup(props) {
  const { ID, setup, allIDs } = props;
  const { char } = props.setup;
  const isOg = props.setup.type === "original";
  const charData = characters[char.name];
  return (
    <Box
      className="mb-3 px-2 pt-3 pb-2"
      borderRadius="0.5rem"
      bgColor="cardBg3"
      boxShadow={props.chosen ? "0 0 5px 3px green" : "var(--cmn-shadow)"}
      onClick={() => props.setChosenStID(ID)}
    >
      <div className="px-2 flex justify-between">
        <Box className="flex align-center" width="24rem">
          {!isOg && <LinkButton uncombine={props.uncombine} />}
          <Text className="ellipsis" variant="h3" color="orange">
            {props.stName || setup.name}
          </Text>
        </Box>
        <div className="flex pb-3">
          <IconBtn
            icon="fas fa-wrench"
            className="mr-4 p-2 glow-on-hover"
            variant="positive"
            onClick={() => props.setImportedSt(reCreateSetup(setup))}
          />
          <IconBtn
            icon="fas fa-share-alt"
            className="mr-4 p-2 glow-on-hover"
            variant="neutral"
            onClick={() => props.setSharedID(setup.ID)}
          />
          {isOg ? (
            <IconBtn
              icon="fas fa-trash-alt"
              className="glow-on-hover"
              variant="negative"
              onClick={() => props.setRemovedID(ID)}
            />
          ) : (
            <AddButton
              addable={Object.keys(allIDs).length < 4}
              add={() => props.setAddingID(ID)}
            />
          )}
        </div>
      </div>
      <div className="flex">
        <Card className="flex">
          <Members
            char={char}
            charData={charData}
            isOg={isOg}
            party={setup.party}
            buildTmSt={(tmI) => props.buildTmSt(setup, tmI)}
            allIDs={allIDs}
            switchMember={props.switchMember}
          />
          <Divider />
          <div>
            <Box className="mb-2 flex justify-center">
              {["Stats", "Modifiers"].map((type, i) => (
                <Button
                  key={i}
                  className="mx-2 glow-on-hover"
                  variant="default"
                  onClick={() => props.setModalType(type)}
                >
                  {type}
                </Button>
              ))}
            </Box>
            <Gears weapon={setup.weapon} artPieces={setup.art.pieces} />
          </div>
        </Card>
      </div>
    </Box>
  );
}

function AddButton({ addable, add }) {
  return (
    <IconBtn
      icon="fas fa-plus"
      className={addable ? "glow-on-hover" : "unavailable"}
      variant="neutral"
      onClick={() => {
        if (addable) add();
      }}
    />
  );
}

function LinkButton({ uncombine }) {
  if (window.innerWidth <= 1050) {
    return (
      <IconBtn
        icon="fas fa-unlink"
        className="mr-2 glow-on-hover"
        variant="negative"
        onClick={uncombine}
      />
    );
  }
  return <PCVer uncombine={uncombine} />;
}

function PCVer(props) {
  const [hovered, setHovered] = useState(false);
  return (
    <LinkIcon
      className={"mr-2 flex-center pointer " + (hovered ? "unlink" : "link")}
      onMouseEnter={() => setHovered(true)}
      onMouseLeave={() => setHovered(false)}
      onClick={props.uncombine}
    >
      <i className={"fas fa-" + (hovered ? "unlink" : "link")} />
    </LinkIcon>
  );
}
