import { memo, useContext, useState } from "react";
import artifacts from "../../../data/artifacts";
import artifactBuffs from "../../../data/artifacts/artifactBuffs";
import { artifactIcons, artTypes, wikia } from "../../../data/misc";
import { deepCopy } from "../../../helpers/misc";
import { getArtSets } from "../../../helpers/processors";
import { Img } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { ArtPicker } from "../../Picker/ItemPicker";
import { getMainArtBCs } from "../helpers";
import { ArtChestCt, FixedCts, ManagerCts, SetupCt } from "../index";
import { CopySection, Pedestal } from "../styles";
import ArtPieceInfo from "./ArtPieceInfo";

export default memo(SectionArts);

function SectionArts() {
  const artChest = useContext(ArtChestCt);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const { setups } = useContext(ManagerCts);
  const { pieces } = artChest[curSetupI];
  const [tabNo, setTabNo] = useState(0);
  const [infoOn, setInfoOn] = useState(false);
  const [pickerOn, setPickerOn] = useState(false);

  const copyArts = (name) => {
    setArtChest((prev) => {
      const chest = deepCopy(prev);
      const index = setups.findIndex((st) => st.name === name);
      chest[curSetupI] = deepCopy(chest[index]);
      return chest;
    });
  };
  const copyOpts = [];
  if (pieces.every((artP) => artP === null)) {
    for (let i in artChest) {
      if (artChest[i].pieces.some((artP) => artP !== null)) {
        copyOpts.push(setups[i].name);
      }
    }
  }
  return (
    <Pedestal>
      {copyOpts.length ? (
        <CopySection options={copyOpts} copy={copyArts} />
      ) : null}
      <TabBar
        pieces={pieces}
        tabNo={tabNo}
        setTabNo={setTabNo}
        infoOn={infoOn}
        setInfoOn={setInfoOn}
      />
      <ArtPieceInfo
        pieces={pieces}
        tabNo={tabNo}
        infoOn={infoOn}
        setPickerOn={setPickerOn}
      />
      {pickerOn && (
        <ArtPicker
          artType={artTypes[tabNo]}
          close={() => setPickerOn(false)}
          pick={(item) =>
            setArtPiece(
              curSetupI,
              tabNo,
              { ID: Date.now(), ...item },
              setArtChest
            )
          }
        />
      )}
    </Pedestal>
  );
}

function TabBar({ pieces, tabNo, setTabNo, infoOn, setInfoOn }) {
  return (
    <div className="flex">
      {artTypes.map((type, index) => {
        const artP = pieces[index];
        let src;
        if (artP) {
          const set = artifacts[artP.set];
          src = set.beta ? set[type].icon : wikia + set[type].icon + ".png";
        } else {
          src = wikia + artifactIcons[type] + ".png";
        }
        return (
          <Box
            key={index}
            minWidth="20%"
            border={
              index === tabNo && infoOn
                ? "2px solid white"
                : "0.5px solid transparent"
            }
          >
            <Box
              className="pointer"
              bgImg={artP ? artP.rarity || 5 : 1}
              onClick={() => {
                setTabNo(index);
                setInfoOn(index === tabNo ? !infoOn : true);
                if (!infoOn) {
                  setTimeout(() => {
                    const body = document.getElementById("setupManager_body");
                    body.scrollTop = 9999;
                  }, 150);
                }
              }}
            >
              <Img width="100%" src={src} alt={type} draggable={false} />
            </Box>
          </Box>
        );
      })}
    </div>
  );
}

// [here, .ArtPieceInfo, ./index]
export function setArtPiece(curSetupI, pIndex, newPiece, setArtChest) {
  setArtChest((prev) => {
    const chest = deepCopy(prev);
    const art = chest[curSetupI];
    let oldBonusLv = art.sets[0]?.bonusLv;

    art.pieces[pIndex] = newPiece;
    art.sets = getArtSets(art.pieces);
    const set = art.sets[0];
    if (set) {
      if (oldBonusLv === 0 && set.bonusLv) {
        art.BCs = getMainArtBCs(set.name);
        let pos = art.subBCs.findIndex((BC) => BC.set === set.name);
        if (pos !== -1) {
          art.subBCs.splice(pos, 1);
        }
      } else if (oldBonusLv && !set.bonusLv) {
        art.BCs = [];
        const oldSetN = prev[curSetupI].sets[0].name;
        if (artifactBuffs[oldSetN]) {
          for (let index in artifactBuffs[oldSetN]) {
            const buff = artifactBuffs[oldSetN][index];
            if (buff.affect !== "self") {
              const node = { set: oldSetN, activated: false, index };
              if (buff.inputTypes) {
                node.inputs = [];
                buff.inputTypes.forEach((type, j) => {
                  if (type === "crystallize") {
                    node.inputs.push("Pyro");
                  }
                });
              }
              art.subBCs.push(node);
            }
          }
        }
      }
    }
    return chest;
  });
}
